import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const AlgebraWordProblemsLesson = () => {
  // Strategy steps component
  const StrategySteps = () => (
    <ol className="list-none pr-0 space-y-2" style={{ counterReset: 'step-counter' }}>
      {[
        { title: ' ', content: '   ,    .         ().' },
        { title: ' ', content: '    ( ) ,   x ( x, y).      .' },
        { title: ' ', content: '       .     "", "", " ", " -", " -" \'.' },
        { title: ' ', content: '   (  )     .' },
        { title: '  ', content: '       (,     ).      .' }
      ].map((step, index) => (
        <li key={index} className="relative pr-8 mb-2">
          <div className="absolute right-0 top-0 w-6 h-6 bg-blue-500 text-white rounded-full flex items-center justify-center text-sm font-bold">
            {index + 1}
          </div>
          <strong>{step.title}:</strong> {step.content}
        </li>
      ))}
    </ol>
  );

  // Solved example component
  const SolvedExample = () => (
    <div className="border border-gray-200 rounded-lg p-4 bg-gray-50">
      <p className="font-medium mb-2">:    2    .    21 .     ?</p>
      <div className="space-y-2">
        <p><strong>:</strong></p>
        <p>1. <strong>:</strong>       .       .</p>
        <p>2. <strong> :</strong></p>
        <p>      -<FormulaBox>\(x\)</FormulaBox>.</p>
        <p>  "   2    ",      <FormulaBox>\(2x\)</FormulaBox>.</p>
        <p>3. <strong> :</strong></p>
        <p> "   21 "  :</p>
        <p><FormulaBox>\(x + 2x = 21\)</FormulaBox> (   +    =   )</p>
        <p>4. <strong> :</strong></p>
        <p><FormulaBox>\(3x = 21\)</FormulaBox></p>
        <p> -3: <FormulaBox>\(x = \frac{21}{3} = 7\)</FormulaBox>.</p>
        <p>5. <strong>  :</strong></p>
        <p> <FormulaBox>\(x=7\)</FormulaBox>.     .</p>
        <p>     <FormulaBox>\(2x = 2 \cdot 7 = 14\)</FormulaBox>.</p>
        <p>:  (7)  (14)   <FormulaBox>\(7+14=21\)</FormulaBox> , -14   2 -7.  .</p>
        <p className="mt-2 font-semibold">:   7 ,   14 .</p>
      </div>
    </div>
  );

  // Custom validation for floating point answers
  const validateFloatAnswer = (userAnswer, correctAnswer, tolerance = 0.1) => {
    const userNum = parseFloat(userAnswer);
    const correctNum = parseFloat(correctAnswer);
    return Math.abs(userNum - correctNum) < tolerance;
  };

  const exercises = [
    {
      id: 'ex1',
      question: '   -5 "  .   3  -4     40 ".   ?',
      hint: '    -x.',
      correctAnswer: '3.57',
      tolerance: 0.1,
      solution: (
        <div>
          <p><strong> :</strong></p>
          <p>:   = <FormulaBox>\(x\)</FormulaBox> ".</p>
          <p>  = <FormulaBox>\(x+5\)</FormulaBox> ".</p>
          <p>: <FormulaBox>\(3(x+5) + 4x = 40\)</FormulaBox></p>
          <p> : <FormulaBox>\(3x + 15 + 4x = 40\)</FormulaBox></p>
          <p> : <FormulaBox>\(7x + 15 = 40\)</FormulaBox></p>
          <p> : <FormulaBox>\(7x = 40 - 15 \Rightarrow 7x = 25\)</FormulaBox></p>
          <p> : <FormulaBox>\(x = \frac{25}{7} \approx 3.57\)</FormulaBox></p>
          <p>   -3.57 ".</p>
        </div>
      ),
      placeholder: '   ()'
    },
    {
      id: 'ex2',
      question: '     48.    ?',
      hint: '     -x.    x, x+1, x+2.',
      correctAnswer: '15',
      solution: (
        <div>
          <p><strong> :</strong></p>
          <p>:   = <FormulaBox>\(x\)</FormulaBox>.</p>
          <p>  = <FormulaBox>\(x+1\)</FormulaBox>.</p>
          <p>  = <FormulaBox>\(x+2\)</FormulaBox>.</p>
          <p>: <FormulaBox>\(x + (x+1) + (x+2) = 48\)</FormulaBox></p>
          <p> : <FormulaBox>\(3x + 3 = 48\)</FormulaBox></p>
          <p> : <FormulaBox>\(3x = 48 - 3 \Rightarrow 3x = 45\)</FormulaBox></p>
          <p> : <FormulaBox>\(x = \frac{45}{3} = 15\)</FormulaBox></p>
          <p>    15.</p>
        </div>
      ),
      placeholder: '    '
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '   -4 " .    32 ".   ?',
      hint: '   -x.',
      options: [
        { value: 'a', label: '4 "' },
        { value: 'b', label: '6 "' },
        { value: 'c', label: '8 "' },
        { value: 'd', label: '10 "' }
      ],
      correctAnswer: 'b',
      explanation: '  = x,  = x+4.  = 2( + ) = 2(x + x+4) = 2(2x+4) = 4x+8 = 32.  4x = 24, x = 6.'
    },
    {
      id: 'q2',
      question: '    .      3   .    28  .     ?',
      options: [
        { value: 'a', label: '7' },
        { value: 'b', label: '9' },
        { value: 'c', label: '14' },
        { value: 'd', label: '21' }
      ],
      correctAnswer: 'a',
      explanation: '   = x,   = 3x.  : x + 3x = 4x = 28.  x = 7.'
    }
  ];

  return (
    <LessonLayout
      title="  ()"
      lessonId="35182-algebra-word-problems"
      nextLessonPath="/lessons/geometry-shapes-properties"
    >
      {/* Learn Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        <div className="space-y-6 text-gray-700 leading-relaxed">
          <p>
                      ,  .    (  ),         .
          </p>
          <p>
             35182,         ,      .
          </p>

          <h3 className="text-xl font-semibold mt-6 mb-2">  :</h3>
          <StrategySteps />

          <h3 className="text-xl font-semibold mt-6 mb-2">:    </h3>          <p> ,     , ,  .   :</p>          <div className="my-4 text-center">
            <FormulaBox>
              {`\\text{Price per Unit} \\times \\text{Quantity} = \\text{Total}`}
            </FormulaBox>
          </div>

          <h4 className="text-lg font-semibold mt-4 mb-1">  (  ):</h4>
          <SolvedExample />
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        <div className="space-y-8">
          {exercises.map((exercise) => (
            <Exercise
              key={exercise.id}
              question={exercise.question}
              hint={exercise.hint}
              correctAnswer={exercise.correctAnswer}
              solution={exercise.solution}
              placeholder={exercise.placeholder}
              tolerance={exercise.tolerance}
              customValidator={exercise.tolerance ? validateFloatAnswer : null}
            />
          ))}
        </div>
      </section>      {/* Quiz Section */}
      <Quiz questions={quizQuestions} />
    </LessonLayout>
  );
};

export default AlgebraWordProblemsLesson;
