import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';

const AnalyticGeometryCircleTangentLesson = () => {
  const exercises = [
    {
      id: 1,
      question: "     x + y = 25  (3, 4)",
      solution: "  : 2x + 2y(dy/dx) = 0,  dy/dx = -x/y.  (3, 4):  = -3/4.  : y - 4 = -3/4(x - 3)  3x + 4y = 25"
    },
    {
      id: 2,
      question: "     (x-2) + (y+1) = 9  (5, -1)",
      solution: "  -(2, -1)  3.   (5, -1)  .   : x = 5"
    }
  ];

  return (
    <LessonLayout 
      title="  -  "
      lessonId="analytic-geometry-circle-tangent"
    >
      <div className="space-y-6">
        {/*  */}
        <div className="bg-blue-50 p-6 rounded-lg border-r-4 border-blue-500">
          <h2 className="text-2xl font-bold text-blue-800 mb-4"> </h2>
          <p className="text-gray-700">
                      .
          </p>
        </div>

        {/*   -  */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4"></h3>
          <p className="mb-4">
            <strong> </strong>         .
          </p>
          
          <div className="bg-blue-50 p-4 rounded-lg">
            <h4 className="font-semibold mb-2"> :</h4>
            <ul className="list-disc list-inside space-y-2">
              <li>         </li>
              <li>       </li>
              <li>      </li>
            </ul>
          </div>
        </div>

        {/*     */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4">   </h3>
          
          <div className="space-y-6">
            <div>
              <h4 className="text-lg font-semibold mb-3"> 1:  </h4>
              <p className="mb-3">
                  <FormulaBox type="inline">x^2 + y^2 = r^2</FormulaBox>:
              </p>
              <FormulaBox type="block">{`2x + 2y \\\\frac{dy}{dx} = 0 \\\\Rightarrow \\\\frac{dy}{dx} = -\\\\frac{x}{y}`}</FormulaBox>
              <p className="mt-3">    <FormulaBox type="inline">{`(x_0, y_0)`}</FormulaBox> :</p>
              <FormulaBox type="block">{`m = -\\\\frac{x_0}{y_0}`}</FormulaBox>
            </div>

            <div>
              <h4 className="text-lg font-semibold mb-3"> 2:  </h4>
              <p className="mb-3">
                  <FormulaBox type="inline">x^2 + y^2 = r^2</FormulaBox>  <FormulaBox type="inline">(x_0, y_0)</FormulaBox>  :
              </p>
              <FormulaBox type="block">
                x_0 x + y_0 y = r^2
              </FormulaBox>
            </div>

            <div>
              <h4 className="text-lg font-semibold mb-3"> 3:   </h4>
              <p className="mb-3">
                  <FormulaBox type="inline">(x-h)^2 + (y-k)^2 = r^2</FormulaBox>  <FormulaBox type="inline">(x_0, y_0)</FormulaBox>:
              </p>
              <FormulaBox type="block">
                (x_0-h)(x-h) + (y_0-k)(y-k) = r^2
              </FormulaBox>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4"></h3>
          
          <div className="space-y-6">
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-semibold mb-3"> 1:   </h4>
              <p className="mb-3">
                     <FormulaBox type="inline">x^2 + y^2 = 25</FormulaBox>  <FormulaBox type="inline">(3, 4)</FormulaBox>.
              </p>
              
              <div className="mr-4">
                <p className="font-medium mb-2">:</p>
                <p className="mb-2">  :</p>
                <FormulaBox type="block">
                  3x + 4y = 25
                </FormulaBox>
                
                <p className="mt-3 mb-2">:  (3, 4)  ?</p>
                <FormulaBox type="block">
                  3^2 + 4^2 = 9 + 16 = 25 
                </FormulaBox>
              </div>
            </div>

            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-semibold mb-3"> 2:  </h4>
              <p className="mb-3">
                     <FormulaBox type="inline">(x-1)^2 + (y+2)^2 = 9</FormulaBox>  <FormulaBox type="inline">(4, -2)</FormulaBox>.
              </p>
              
              <div className="mr-4">
                <p className="font-medium mb-2">:</p>
                <p className="mb-2">  -(1, -2)  3.</p>
                <p className="mb-2"> :</p>
                <FormulaBox type="block">
                  (4-1)(x-1) + (-2-(-2))(y-(-2)) = 9
                </FormulaBox>
                <FormulaBox type="block">
                  3(x-1) + 0(y+2) = 9
                </FormulaBox>
                <FormulaBox type="block">
                  3(x-1) = 9 \Rightarrow x = 4
                </FormulaBox>
                <p className="mt-2">    x = 4.</p>
              </div>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4"></h3>
          <div className="space-y-4">
            {exercises.map((exercise) => (
              <Exercise key={exercise.id} {...exercise} />
            ))}
          </div>
        </div>

        {/*    */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4">   </h3>
          
          <p className="mb-4">
                    .
          </p>
          
          <div className="bg-yellow-50 p-4 rounded-lg">
            <h4 className="font-semibold mb-2">  :</h4>
            <ol className="list-decimal list-inside space-y-2">
              <li>      -<FormulaBox type="inline">(x_0, y_0)</FormulaBox></li>
              <li>  ,     </li>
              <li>     </li>
              <li>      </li>
            </ol>
          </div>
        </div>

        {/*   */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4"> </h3>
          
          <div className="space-y-4">
            <div className="bg-red-50 p-4 rounded-lg">
              <h4 className="font-semibold mb-2"> :</h4>
              <p> <FormulaBox type="inline">y_0 = 0</FormulaBox>,   <FormulaBox type="inline">x = x_0</FormulaBox></p>
            </div>
            
            <div className="bg-green-50 p-4 rounded-lg">
              <h4 className="font-semibold mb-2"> :</h4>
              <p> <FormulaBox type="inline">x_0 = 0</FormulaBox>,   <FormulaBox type="inline">y = y_0</FormulaBox></p>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="bg-gradient-to-r from-blue-50 to-purple-50 p-6 rounded-lg">
          <h3 className="text-xl font-semibold mb-4"></h3>
          <ul className="list-disc list-inside space-y-2">
            <li>        </li>
            <li>  <FormulaBox type="inline">x^2 + y^2 = r^2</FormulaBox>  <FormulaBox type="inline">(x_0, y_0)</FormulaBox>: <FormulaBox type="inline">x_0 x + y_0 y = r^2</FormulaBox></li>
            <li>     </li>
            <li>      </li>
          </ul>
        </div>
      </div>
    </LessonLayout>
  );
};

export default AnalyticGeometryCircleTangentLesson;
