import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const CalculusIntegralPolynomialLesson = () => {
  const exercises = [
    {
      id: 'ex1',
      question: '      ',
      formula: 'f(x) = 4x^3 - 6x + 2',
      fields: [
        { id: 'answer', label: ' ', placeholder: '   ( C)' }
      ],
      solution: (
        <div>
          <p><strong> :</strong></p>
          <FormulaBox>{"\\int (4x^3 - 6x + 2) dx = 4\\frac{x^4}{4} - 6\\frac{x^2}{2} + 2x + C"}</FormulaBox>
          <FormulaBox>= x^4 - 3x^2 + 2x + C</FormulaBox>
          <p><strong>:</strong> <FormulaBox inline>x^4 - 3x^2 + 2x + C</FormulaBox>.</p>
        </div>
      ),
      checkAnswer: (answers) => {
        const answer = answers.answer?.toLowerCase().replace(/\s/g, '');
        const correctAnswer = answer.includes('x^4-3x^2+2x+c') || 
                             answer.includes('x^4-3x+2x+c') ||
                             answer.includes('x-3x+2x+c');
        
        if (correctAnswer) {
          return { correct: true, message: ' !  .' };
        } else {
          return { correct: false, message: ' .           C.' };
        }
      }
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '   ',
      formula: 'f(x) = 2x',
      options: [
        { value: 'a', label: '2x + C' },
        { value: 'b', label: 'x + C' },
        { value: 'c', label: '2 + C' }
      ],
      correct: 'b',
      explanation: '  : 2x dx = 2x dx = 2(x/2) + C = x + C.'
    },
    {
      id: 'q2',
      question: '   :',
      formula: ' x dx',
      options: [
        { value: 'a', label: '1' },
        { value: 'b', label: '1/3' },
        { value: 'c', label: '3' }
      ],
      correct: 'b',
      explanation: '   x  x/3. : [x/3] = (1/3) - (0/3) = 1/3 - 0 = 1/3.'
    },
    {
      id: 'q3',
      question: '       f(x) = 5?',
      options: [
        { value: 'a', label: '5x + C' },
        { value: 'b', label: '5 + C' },
        { value: 'c', label: 'x + C' }
      ],
      correct: 'a',
      explanation: '   k  kx + C.  5 dx = 5x + C.'
    }
  ];

  return (
    <LessonLayout
      title="  () "
      lessonId="calculus-integral-polynomial"
    >
      <div className="space-y-6">
        {/*  */}
        <div className="bg-green-50 p-6 rounded-lg border-r-4 border-green-500">
          <h2 className="text-2xl font-bold text-green-800 mb-4">  -  </h2>
          <p className="text-gray-700">
                   .
          </p>
        </div>

        {/*   */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4">  (  )</h3>
          <p className="mb-4">
                <FormulaBox inline>f(x)</FormulaBox>   <FormulaBox inline>F(x)</FormulaBox>    -<FormulaBox inline>f(x)</FormulaBox>, 
             <FormulaBox inline>F'(x) = f(x)</FormulaBox>.
          </p>
          <p className="mb-4">
                <FormulaBox inline>f(x)</FormulaBox>  <FormulaBox inline>\\int f(x) dx</FormulaBox>        <FormulaBox inline>f(x)</FormulaBox>. 
             <FormulaBox inline>F(x)</FormulaBox>    ,        <FormulaBox inline>F(x) + C</FormulaBox>, 
             <FormulaBox inline>C</FormulaBox>   .
          </p>
          
          <h4 className="text-lg font-semibold mt-4 mb-1 text-gray-800">   :</h4>
          <ul className="list-disc pr-6 space-y-2">
            <li><FormulaBox>{"\\int x^n dx = \\frac{x^{n+1}}{n+1} + C \\quad (n \\neq -1)"}</FormulaBox></li>
            <li><FormulaBox>{"\\int k dx = kx + C \\quad (k \\text{ constant})"}</FormulaBox></li>            <li><FormulaBox>{"\\int k \\cdot f(x) dx = k \\int f(x) dx"}</FormulaBox></li>
            <li><FormulaBox>{"\\int (f(x) \\pm g(x)) dx = \\int f(x) dx \\pm \\int g(x) dx"}</FormulaBox></li>
          </ul>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-4">
            <p className="font-medium">
              <strong>:</strong>     <FormulaBox inline>f(x) = 3x^2 - 2x + 5</FormulaBox>.
            </p>
            <p><strong>:</strong></p>            <FormulaBox>{"\\int (3x^2 - 2x + 5) dx = 3 \\int x^2 dx - 2 \\int x dx + \\int 5 dx"}</FormulaBox>
            <FormulaBox>{"= 3 \\left(\\frac{x^{3}}{3}\\right) - 2 \\left(\\frac{x^{2}}{2}\\right) + 5x + C"}</FormulaBox>
            <FormulaBox>{"= x^3 - x^2 + 5x + C"}</FormulaBox>
          </div>
        </div>

        {/*   */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4">   </h3>
          <p className="mb-4">
                <FormulaBox inline>f(x)</FormulaBox>  <FormulaBox inline>a</FormulaBox>   <FormulaBox inline>b</FormulaBox>  <FormulaBox inline>\\int_a^b f(x) dx</FormulaBox>  :
          </p>
          <FormulaBox>\int_a^b f(x) dx = [F(x)]_a^b = F(b) - F(a)</FormulaBox>
          <p className="mb-4">
             <FormulaBox inline>F(x)</FormulaBox>      <FormulaBox inline>f(x)</FormulaBox> (   <FormulaBox inline>C</FormulaBox>    ).
          </p>
          
          <h4 className="text-lg font-semibold mt-4 mb-1 text-gray-800">       -x:</h4>
          <p className="mb-4">
              <FormulaBox inline>f(x)</FormulaBox>  - (<FormulaBox inline>f(x) \\ge 0</FormulaBox>)  <FormulaBox inline>[a,b]</FormulaBox>, 
             <FormulaBox inline>S</FormulaBox>    ,  -x,  <FormulaBox inline>x=a</FormulaBox> -<FormulaBox inline>x=b</FormulaBox> :
          </p>
          <FormulaBox>S = \int_a^b f(x) dx</FormulaBox>
          <p className="mb-4">
               (<FormulaBox inline>f(x) \\le 0</FormulaBox>) ,   <FormulaBox inline>S = \\int_a^b (-f(x)) dx = -\\int_a^b f(x) dx</FormulaBox>, 
              <FormulaBox inline>|\\int_a^b f(x) dx|</FormulaBox>.
          </p>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-4">
            <p className="font-medium">
              <strong>:</strong>         <FormulaBox inline>y = -x^2 + 4x</FormulaBox>, 
               -x,  <FormulaBox inline>x=1</FormulaBox> -<FormulaBox inline>x=3</FormulaBox>.
            </p>
            <p><strong>:</strong></p>
            <p>
              1.      <FormulaBox inline>[1,3]</FormulaBox>.     x: <FormulaBox inline>-x^2+4x=0 \\Rightarrow x(-x+4)=0 \\Rightarrow x=0, x=4</FormulaBox>.
            </p>
            <p>
                 "".  0 -4  .  <FormulaBox inline>[1,3]</FormulaBox>    , 
               <FormulaBox inline>f(x) \\ge 0</FormulaBox>  .
            </p>            <p>2.  <FormulaBox inline>{"S = \\int_1^3 (-x^2 + 4x) dx"}</FormulaBox>.</p>
            <p>3.   : <FormulaBox inline>{"F(x) = \\int (-x^2 + 4x) dx = -\\frac{x^3}{3} + 2x^2"}</FormulaBox>.</p>
            <p>4.    :</p>
            <FormulaBox>{"S = [-\\frac{x^3}{3} + 2x^2]_1^3"}</FormulaBox>
            <FormulaBox>{"= \\left(-\\frac{27}{3} + 2 \\cdot 9\\right) - \\left(-\\frac{1}{3} + 2\\right)"}</FormulaBox>
            <FormulaBox>{"= (-9 + 18) - (-\\frac{1}{3} + \\frac{6}{3}) = 9 - \\frac{5}{3} = \\frac{22}{3}"}</FormulaBox>
            <p><strong>:</strong>   <FormulaBox inline>\\frac{22}{3}</FormulaBox>  .</p>
          </div>

          <h4 className="text-lg font-semibold mt-4 mb-1 text-gray-800">     :</h4>
          <p className="mb-4">
             <FormulaBox inline>S</FormulaBox>       <FormulaBox inline>f(x)</FormulaBox> -<FormulaBox inline>g(x)</FormulaBox>  <FormulaBox inline>[a,b]</FormulaBox>, 
             <FormulaBox inline>f(x) \\ge g(x)</FormulaBox>  , :
          </p>
          <FormulaBox>S = \int_a^b (f(x) - g(x)) dx</FormulaBox>
          <p> <FormulaBox inline>a,b</FormulaBox>    -x      .</p>
        </div>

        {/*  */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4"></h3>
          <div className="space-y-4">
            {exercises.map((exercise) => (
              <Exercise key={exercise.id} {...exercise} />
            ))}
          </div>
        </div>

        {/*    */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4">   </h3>
          <Quiz questions={quizQuestions} />
        </div>

        {/*  */}
        <div className="bg-gradient-to-r from-green-50 to-blue-50 p-6 rounded-lg">
          <h3 className="text-xl font-semibold mb-4"></h3>
          <ul className="list-disc list-inside space-y-2">
            <li>       </li>
            <li>    </li>
            <li>     </li>
            <li>      </li>
            <li>       </li>
          </ul>
        </div>
      </div>
    </LessonLayout>
  );
};

export default CalculusIntegralPolynomialLesson;
