import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const AlgebraQuadraticEquationsLesson = () => {
  const exercises = [
    {
      id: 'ex1',
      question: '   \\(2x^2 + 5x - 3 = 0\\).      (: 1,-2).',
      correctAnswer: '0.5,-3',
      tolerance: 0.01,
      allowFractions: true,
      solution: (
        <div>
          <p><strong> :</strong></p>
          <p> : <FormulaBox inline>2x^2 + 5x - 3 = 0</FormulaBox></p>
          <p> : <FormulaBox inline>a = 2, b = 5, c = -3</FormulaBox></p>
          <p>  :</p>
          <FormulaBox>{"x_{1,2} = \\frac{-5 \\pm \\sqrt{5^2 - 4 \\cdot 2 \\cdot (-3)}}{2 \\cdot 2}"}</FormulaBox>
          <FormulaBox>{"x_{1,2} = \\frac{-5 \\pm \\sqrt{25 + 24}}{4} = \\frac{-5 \\pm \\sqrt{49}}{4} = \\frac{-5 \\pm 7}{4}"}</FormulaBox>
          <p> :</p>
          <FormulaBox>{"x_1 = \\frac{-5 + 7}{4} = \\frac{2}{4} = 0.5"}</FormulaBox>
          <FormulaBox>{"x_2 = \\frac{-5 - 7}{4} = \\frac{-12}{4} = -3"}</FormulaBox>
          <p> : <FormulaBox inline>2(0.5)^2 + 5(0.5) - 3 = 0.5 + 2.5 - 3 = 0 </FormulaBox></p>
          <p>-<FormulaBox inline>2(-3)^2 + 5(-3) - 3 = 18 - 15 - 3 = 0 </FormulaBox></p>
        </div>
      )
    },
    {
      id: 'ex2',
      question: '     \\(x^2 - 4x + 1 = 0\\)     .',
      correctAnswer: '12',
      tolerance: 0.01,
      solution: (
        <div>
          <p><strong>:</strong></p>
          <p> : <FormulaBox inline>x^2 - 4x + 1 = 0</FormulaBox></p>
          <p> : <FormulaBox inline>a = 1, b = -4, c = 1</FormulaBox></p>
          <p>:</p>
          <FormulaBox>\Delta = b^2 - 4ac = (-4)^2 - 4 \cdot 1 \cdot 1 = 16 - 4 = 12</FormulaBox>
          <p> -<FormulaBox inline>\Delta = 12 &gt; 0</FormulaBox>,      .</p>
        </div>
      )
    },
    {
      id: 'ex3',
      question: '    \\(3x^2 - 12 = 0\\).    .',
      correctAnswer: '2',
      tolerance: 0.01,
      solution: (
        <div>
          <p><strong>:</strong></p>
          <p> : <FormulaBox inline>3x^2 - 12 = 0</FormulaBox></p>
          <p>   (  x).    :</p>          <FormulaBox>3x^2 = 12</FormulaBox>
          <FormulaBox>x^2 = 4</FormulaBox>
          <FormulaBox>x = \pm 2</FormulaBox>
          <p>  <FormulaBox inline>x = 2</FormulaBox> -<FormulaBox inline>x = -2</FormulaBox>.</p>
          <p>   2.</p>
        </div>
      )
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '     ?',
      options: [
        '  ',
        '   ',
        '  ',
        ' '
      ],
      correctAnswer: '   ',
      explanation: '      :    -  ,   -  ,   -   .'
    },
    {
      id: 'q2',
      question: '    ax + c = 0?',
      options: [
        '  ',
        '  ',
        ' x  ',
        '  '
      ],
      correctAnswer: ' x  ',
      explanation: '   ax + c = 0     x: x = -c/a,   x = (-c/a).'
    },
    {
      id: 'q3',
      question: '      ?',
      options: [
        '  ',
        '  (   )',
        '  ',
        ' '
      ],
      correctAnswer: '  (   )',
      explanation: '   ,        (   ).'
    }
  ];

  return (
    <LessonLayout
      title=" "
      lessonId="35182-algebra-quadratic-equations"
      nextLessonPath="/lesson/35182-algebra-inequalities"
    >
      {/* Learn Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2"> </h2>
        <div className="space-y-6 text-gray-700 leading-relaxed">
          <p>     , ,         .      :</p>
          <FormulaBox>ax^2 + bx + c = 0</FormulaBox>
          <p> <FormulaBox inline>a, b, c</FormulaBox>    ( ),    <FormulaBox inline>a \\neq 0</FormulaBox> (   ).</p>

          <h3 className="text-xl font-semibold mt-6 mb-2">     </h3>
          <p>        <strong> </strong>.       <FormulaBox inline>x</FormulaBox> (  )   :</p>
          <FormulaBox>{"x_{1,2} = \\frac{-b \\pm \\sqrt{b^2-4ac}}{2a}"}</FormulaBox>
          <p> <FormulaBox inline>\\pm</FormulaBox>      (     ),  ,    .</p>

          <h4 className="text-lg font-semibold mt-4 mb-1"> :</h4>
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50">
            <p className="font-medium">:    <FormulaBox inline>x^2 - 5x + 6 = 0</FormulaBox>.</p>
            <p className="mt-2"><strong>:</strong></p>
            <p> ,  : <FormulaBox inline>a=1, b=-5, c=6</FormulaBox>.</p>
            <p>    :</p>
            <FormulaBox>{"x_{1,2} = \\frac{-(-5) \\pm \\sqrt{(-5)^2 - 4 \\cdot 1 \\cdot 6}}{2 \\cdot 1}"}</FormulaBox>
            <FormulaBox>{"x_{1,2} = \\frac{5 \\pm \\sqrt{25 - 24}}{2} = \\frac{5 \\pm \\sqrt{1}}{2} = \\frac{5 \\pm 1}{2}"}</FormulaBox>
            <p>    :</p>
            <p><FormulaBox inline>x_1 = \\frac{5+1}{2} = \\frac{6}{2} = 3</FormulaBox></p>
            <p><FormulaBox inline>x_2 = \\frac{5-1}{2} = \\frac{4}{2} = 2</FormulaBox></p>
            <p className="mt-2">,    <FormulaBox inline>x=3</FormulaBox> -<FormulaBox inline>x=2</FormulaBox>.</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2">   </h3>
          <p>     , <FormulaBox inline>b^2-4ac</FormulaBox>,  <strong></strong>     <FormulaBox inline>\\Delta</FormulaBox>.</p>
          <FormulaBox>\Delta = b^2-4ac</FormulaBox>
          <p>        :</p>
          <ul className="list-disc list-inside space-y-2 pr-5">            <li> <FormulaBox inline>\Delta &gt; 0</FormulaBox>:     .</li>
            <li> <FormulaBox inline>\Delta = 0</FormulaBox>:     (    ).</li>
            <li> <FormulaBox inline>\Delta &lt; 0</FormulaBox>:    .</li>
          </ul>

          <h4 className="text-lg font-semibold mt-4 mb-1"> :</h4>
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3">
            <p><strong>1.  :</strong>  <FormulaBox inline>x^2 - 5x + 6 = 0</FormulaBox> ( ):</p>
            <p><FormulaBox inline>\Delta = (-5)^2 - 4 \cdot 1 \cdot 6 = 25 - 24 = 1</FormulaBox>.  -<FormulaBox inline>\Delta &gt; 0</FormulaBox>,   .</p>
            
            <p><strong>2.  :</strong>  <FormulaBox inline>x^2 - 6x + 9 = 0</FormulaBox>:</p>
            <p><FormulaBox inline>\\Delta = (-6)^2 - 4 \\cdot 1 \\cdot 9 = 36 - 36 = 0</FormulaBox>.  -<FormulaBox inline>\\Delta = 0</FormulaBox>,    (<FormulaBox inline>x=3</FormulaBox>).</p>

            <p><strong>3.   :</strong>  <FormulaBox inline>x^2 + 2x + 5 = 0</FormulaBox>:</p>
            <p><FormulaBox inline>\Delta = (2)^2 - 4 \cdot 1 \cdot 5 = 4 - 20 = -16</FormulaBox>.  -<FormulaBox inline>\Delta &lt; 0</FormulaBox>,   .</p>
          </div>
          
          <h3 className="text-xl font-semibold mt-6 mb-2">  </h3>
          <p>      <FormulaBox inline>b</FormulaBox>  <FormulaBox inline>c</FormulaBox> ( ,   <FormulaBox inline>x=0</FormulaBox>)  .</p>
          <ul className="list-disc list-inside space-y-2 pr-5">
            <li><strong> <FormulaBox inline>b=0</FormulaBox>:</strong>   <FormulaBox inline>ax^2+c=0</FormulaBox>.     <FormulaBox inline>x^2</FormulaBox>  .
              <br />: <FormulaBox inline>2x^2 - 18 = 0 \\Rightarrow 2x^2 = 18 \\Rightarrow x^2 = 9 \\Rightarrow x = \\pm 3</FormulaBox>.
            </li>
            <li><strong> <FormulaBox inline>c=0</FormulaBox>:</strong>   <FormulaBox inline>ax^2+bx=0</FormulaBox>.       <FormulaBox inline>x</FormulaBox>.
              <br />: <FormulaBox inline>3x^2 - 6x = 0 \\Rightarrow x(3x - 6) = 0</FormulaBox>.  <FormulaBox inline>x=0</FormulaBox>  <FormulaBox inline>3x-6=0 \\Rightarrow 3x=6 \\Rightarrow x=2</FormulaBox>.
            </li>
          </ul>
          <p>            <FormulaBox inline>b=0</FormulaBox>  <FormulaBox inline>c=0</FormulaBox> .</p>
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2"> </h2>
        <div className="space-y-6">
          {exercises.map((exercise, index) => (
            <Exercise key={exercise.id} {...exercise} />
          ))}
        </div>
      </section>

      {/* Quiz Section */}
      <section className="mb-12">        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">  </h2>
        <Quiz questions={quizQuestions} />
      </section>
    </LessonLayout>
  );
};

export default AlgebraQuadraticEquationsLesson;
