import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const CalculusPolynomialLesson = () => {
  const exercises = [
    {
      id: 'ex1',
      question: ' ',
      formula: 'f(x) = x^3 - 6x^2 + 5',
      description: '         .',
      fields: [
        { id: 'max', label: '  (: (1,5))', placeholder: '  ' },
        { id: 'min', label: '  (: (3,-2))', placeholder: '  ' }
      ],
      solution: (
        <div>
          <p><strong> :</strong></p>
          <p>1. <FormulaBox inline>f(x) = x^3 - 6x^2 + 5</FormulaBox></p>
          <p>2. <FormulaBox inline>f'(x) = 3x^2 - 12x</FormulaBox></p>
          <p>3.   -0: <FormulaBox inline>3x^2 - 12x = 0 \\Rightarrow 3x(x-4) = 0</FormulaBox>.</p>
          <p>: <FormulaBox inline>x_1=0, x_2=4</FormulaBox>.</p>
          <p>4.  : <FormulaBox inline>f''(x) = 6x - 12</FormulaBox>.</p>
          <ul className="list-disc pr-4 space-y-2">
            <li> <FormulaBox inline>x=0</FormulaBox>: <FormulaBox inline>f''(0) = -12 &lt; 0</FormulaBox> ().<br/>
                <FormulaBox inline>f(0) = 0^3 - 6(0)^2 + 5 = 5</FormulaBox>.  : <FormulaBox inline>(0,5)</FormulaBox>.</li>
            <li> <FormulaBox inline>x=4</FormulaBox>: <FormulaBox inline>f''(4) = 6(4) - 12 = 24 - 12 = 12 &gt; 0</FormulaBox> ().<br/>
                <FormulaBox inline>f(4) = 4^3 - 6(4)^2 + 5 = 64 - 96 + 5 = -27</FormulaBox>.  : <FormulaBox inline>(4,-27)</FormulaBox>.</li>
          </ul>
          <p><strong>:</strong>  -<FormulaBox inline>(0,5)</FormulaBox>,  -<FormulaBox inline>(4,-27)</FormulaBox>.</p>
        </div>
      ),
      checkAnswer: (answers) => {
        const max = answers.max?.toLowerCase().replace(/\s/g, '');
        const min = answers.min?.toLowerCase().replace(/\s/g, '');
        
        const maxCorrect = max.includes('(0,5)') || max.includes('0,5');
        const minCorrect = min.includes('(4,-27)') || min.includes('4,-27');
        
        if (maxCorrect && minCorrect) {
          return { correct: true, message: ' !   .' };
        } else {
          let feedback = ' . ';
          if (!maxCorrect) feedback += '   .     . ';
          if (!minCorrect) feedback += '   .     . ';
          return { correct: false, message: feedback };
        }
      }
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '   ',
      formula: 'f(x) = 3x^4 - 2x^3 + x',
      options: [
        { value: 'a', label: '12x - 6x + 1' },
        { value: 'b', label: '12x - 6x + x' },
        { value: 'c', label: '3x - 2x + 1' }
      ],
      correct: 'a',
      explanation: ': (3x) = 12x, (-2x) = -6x, (x) = 1.  f(x) = 12x - 6x + 1.'
    },
    {
      id: 'q2', 
      question: '  ,    ,   ?',
      options: [
        { value: 'a', label: ' ' },
        { value: 'b', label: ' ' },
        { value: 'c', label: ' ' }
      ],
      correct: 'a',
      explanation: ' f(x) > 0  ,         .'
    },
    {
      id: 'q3',
      question: '  f(x) = x - 4x + 3,      y?',
      options: [
        { value: 'a', label: '(0, 3)' },
        { value: 'b', label: '(0, -4)' },
        { value: 'c', label: '(3, 0)' }
      ],
      correct: 'a',
      explanation: '    y   x=0. f(0) = 0 - 4(0) + 3 = 3.   (0,3).'
    }
  ];

  return (
    <LessonLayout
      title="  () "
      breadcrumbs={[
        { label: ' ', href: '/' },
        { label: ' 35382', href: '/lessons' },
        { label: '   ()' },
        { label: ' ' }
      ]}
    >
      {/* Learn Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
          :   
        </h2>
        <div className="space-y-4 text-gray-700 leading-relaxed">
          <p>
                 <FormulaBox inline>f(x) = a_n x^n + a_{'{n-1}'}x^{'{n-1}'} + \\dots + a_1 x + a_0</FormulaBox>, 
             <FormulaBox inline>n</FormulaBox>    -  <FormulaBox inline>a_i</FormulaBox>   . 
                    .
          </p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">1.      </h3>
          <ul className="list-disc pr-6 space-y-1">
            <li><strong> :</strong>           (<FormulaBox inline>\\mathbb{'{R}'}</FormulaBox>), , " <FormulaBox inline>x</FormulaBox>".</li>
            <li><strong>   -y:</strong>   <FormulaBox inline>x=0</FormulaBox>.    <FormulaBox inline>f(0) = a_0</FormulaBox> ( ).   <FormulaBox inline>(0, a_0)</FormulaBox>.</li>
            <li><strong>   -x:</strong>   <FormulaBox inline>y=0</FormulaBox> ( <FormulaBox inline>f(x)=0</FormulaBox>).       <FormulaBox inline>a_n x^n + \\dots + a_0 = 0</FormulaBox>.</li>
          </ul>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-4">
            <p className="font-medium">
              <strong> ( ):</strong>   <FormulaBox inline>y = x^2 - 4x + 3</FormulaBox>,      .
            </p>
            <p><strong>:</strong></p>
            <p>   y ( <FormulaBox inline>x=0</FormulaBox>): <FormulaBox inline>y = 0^2 - 4(0) + 3 = 3</FormulaBox>. : <FormulaBox inline>(0,3)</FormulaBox>.</p>
            <p>   x ( <FormulaBox inline>y=0</FormulaBox>): <FormulaBox inline>x^2 - 4x + 3 = 0</FormulaBox>.</p>
            <p> (,  ): <FormulaBox inline>(x-1)(x-3) = 0</FormulaBox>.</p>
            <p>, <FormulaBox inline>x_1 = 1, x_2 = 3</FormulaBox>. : <FormulaBox inline>(1,0), (3,0)</FormulaBox>.</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">2.    </h3>
          <p>  , <FormulaBox inline>f'(x)</FormulaBox>,        <FormulaBox inline>x</FormulaBox>.</p>
          <p><strong>   :</strong></p>
          <ul className="list-disc pr-6 space-y-1">
            <li>  : <FormulaBox inline>(x^n)' = nx^{'{n-1}'}</FormulaBox></li>
            <li>  : <FormulaBox inline>(c)' = 0</FormulaBox></li>
            <li>    : <FormulaBox inline>(c \\cdot f(x))' = c \\cdot f'(x)</FormulaBox></li>
            <li>  / : <FormulaBox inline>(f(x) \\pm g(x))' = f'(x) \\pm g'(x)</FormulaBox></li>
          </ul>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-4">
            <p className="font-medium">
              <strong> ( ):</strong>    <FormulaBox inline>f(x) = 2x^3 - 5x^2 + 7x - 1</FormulaBox>.
            </p>
            <p><strong>:</strong></p>
            <FormulaBox>f'(x) = (2x^3)' - (5x^2)' + (7x)' - (1)'</FormulaBox>
            <FormulaBox>f'(x) = 2 \\cdot 3x^{'{3-1}'} - 5 \\cdot 2x^{'{2-1}'} + 7 \\cdot 1x^{'{1-1}'} - 0</FormulaBox>
            <FormulaBox>f'(x) = 6x^2 - 10x + 7</FormulaBox>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">3.    </h3>
          <p>       <FormulaBox inline>f(x)</FormulaBox>   -<FormulaBox inline>x</FormulaBox>   <FormulaBox inline>x_0</FormulaBox>:</p>
          <ol className="list-decimal pr-6 space-y-1">
            <li>   -<FormulaBox inline>y</FormulaBox>   : <FormulaBox inline>y_0 = f(x_0)</FormulaBox>.    <FormulaBox inline>(x_0, y_0)</FormulaBox>.</li>
            <li>   <FormulaBox inline>f'(x)</FormulaBox>.</li>
            <li>     <FormulaBox inline>x_0</FormulaBox>: <FormulaBox inline>m = f'(x_0)</FormulaBox>.</li>
            <li>   <FormulaBox inline>(x_0, y_0)</FormulaBox>  <FormulaBox inline>m</FormulaBox>   : <FormulaBox inline>y - y_0 = m(x - x_0)</FormulaBox>.</li>
          </ol>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-4">
            <p className="font-medium">
              <strong> ( ):</strong>       <FormulaBox inline>f(x) = x^2 - 3x + 5</FormulaBox>   <FormulaBox inline>x=2</FormulaBox>.
            </p>
            <p><strong>:</strong></p>
            <p>1. <FormulaBox inline>y_0 = f(2) = 2^2 - 3(2) + 5 = 4 - 6 + 5 = 3</FormulaBox>.  : <FormulaBox inline>(2,3)</FormulaBox>.</p>
            <p>2. <FormulaBox inline>f'(x) = (x^2)' - (3x)' + (5)' = 2x - 3</FormulaBox>.</p>
            <p>3. <FormulaBox inline>m = f'(2) = 2(2) - 3 = 4 - 3 = 1</FormulaBox>.</p>
            <p>4.  : <FormulaBox inline>y - 3 = 1(x - 2) \\Rightarrow y - 3 = x - 2 \\Rightarrow y = x + 1</FormulaBox>.</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">4.  :     </h3>
          <ul className="list-disc pr-6 space-y-1">
            <li><strong>   ( ):</strong>      (<FormulaBox inline>f'(x) = 0</FormulaBox>).</li>
            <li><strong>   (/ ):</strong>
              <ul className="list-circle pr-6 space-y-1 mt-1">
                <li><strong>   ( ):</strong>        .       -  .    -  .</li>
                <li><strong>   <FormulaBox inline>f''(x)</FormulaBox>:</strong>
                  <ul className="list-disc pr-6 space-y-1 mt-1">
                    <li> <FormulaBox inline>f''(x_0) &gt; 0</FormulaBox>   <FormulaBox inline>x_0</FormulaBox>,   .</li>
                    <li> <FormulaBox inline>f''(x_0) &lt; 0</FormulaBox>,   .</li>
                    <li> <FormulaBox inline>f''(x_0) = 0</FormulaBox>,     (,  ).</li>
                  </ul>
                </li>
              </ul>
            </li>
            <li><strong>  :</strong>
              <ul className="list-disc pr-6 space-y-1 mt-1">
                <li> <strong></strong>   <FormulaBox inline>f'(x) &gt; 0</FormulaBox>.</li>
                <li> <strong></strong>   <FormulaBox inline>f'(x) &lt; 0</FormulaBox>.</li>
              </ul>
            </li>
          </ul>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-4">
            <p className="font-medium">
              <strong> ( ):</strong>    <FormulaBox inline>f(x) = x^3 - 3x^2</FormulaBox>         .
            </p>
            <p><strong>:</strong></p>
            <p>1.   : <FormulaBox inline>f'(x) = 3x^2 - 6x</FormulaBox>.</p>
            <p>2.     (  -0):</p>
            <FormulaBox>3x^2 - 6x = 0 \\Rightarrow 3x(x - 2) = 0</FormulaBox>
            <p>  <FormulaBox inline>x_1 = 0</FormulaBox>, <FormulaBox inline>x_2 = 2</FormulaBox>.</p>
            <p>3.      : <FormulaBox inline>f''(x) = (3x^2 - 6x)' = 6x - 6</FormulaBox>.</p>
            <ul className="list-disc pr-4 space-y-2">
              <li> <FormulaBox inline>x=0</FormulaBox>: <FormulaBox inline>f''(0) = 6(0) - 6 = -6 &lt; 0</FormulaBox>. , -<FormulaBox inline>x=0</FormulaBox>   <strong></strong>.<br/>
                   -y: <FormulaBox inline>f(0) = 0^3 - 3(0^2) = 0</FormulaBox>.  : <FormulaBox inline>(0,0)</FormulaBox>.</li>
              <li> <FormulaBox inline>x=2</FormulaBox>: <FormulaBox inline>f''(2) = 6(2) - 6 = 12 - 6 = 6 &gt; 0</FormulaBox>. , -<FormulaBox inline>x=2</FormulaBox>   <strong></strong>.<br/>
                   -y: <FormulaBox inline>f(2) = 2^3 - 3(2^2) = 8 - 12 = -4</FormulaBox>.  : <FormulaBox inline>(2,-4)</FormulaBox>.</li>
            </ul>
            <p>4.    (    <FormulaBox inline>f'(x) = 3x(x-2)</FormulaBox>,   ""):</p>
            
            <div className="overflow-x-auto">
              <table className="w-full mt-4 mb-4 border-collapse border border-gray-300">
                <thead>
                  <tr>
                    <th className="border border-gray-300 px-3 py-2 bg-purple-100"></th>
                    <th className="border border-gray-300 px-3 py-2 bg-purple-100"></th>
                    <th className="border border-gray-300 px-3 py-2 bg-purple-100"> f'(x)</th>
                    <th className="border border-gray-300 px-3 py-2 bg-purple-100"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="border border-gray-300 px-3 py-2 text-center"><FormulaBox inline>x &lt; 0</FormulaBox></td>
                    <td className="border border-gray-300 px-3 py-2 text-center">-1</td>
                    <td className="border border-gray-300 px-3 py-2 text-center"><FormulaBox inline>3(-1)(-1-2) = (+)</FormulaBox></td>
                    <td className="border border-gray-300 px-3 py-2 text-center text-green-600"> </td>
                  </tr>
                  <tr>
                    <td className="border border-gray-300 px-3 py-2 text-center"><FormulaBox inline>0 &lt; x &lt; 2</FormulaBox></td>
                    <td className="border border-gray-300 px-3 py-2 text-center">1</td>
                    <td className="border border-gray-300 px-3 py-2 text-center"><FormulaBox inline>3(1)(1-2) = (-)</FormulaBox></td>
                    <td className="border border-gray-300 px-3 py-2 text-center text-red-600"> </td>
                  </tr>
                  <tr>
                    <td className="border border-gray-300 px-3 py-2 text-center"><FormulaBox inline>x &gt; 2</FormulaBox></td>
                    <td className="border border-gray-300 px-3 py-2 text-center">3</td>
                    <td className="border border-gray-300 px-3 py-2 text-center"><FormulaBox inline>3(3)(3-2) = (+)</FormulaBox></td>
                    <td className="border border-gray-300 px-3 py-2 text-center text-green-600"> </td>
                  </tr>
                </tbody>
              </table>
            </div>
            
            <p><strong>:</strong>   <FormulaBox inline>(0,0)</FormulaBox>,   <FormulaBox inline>(2,-4)</FormulaBox>.</p>
            <p> : <FormulaBox inline>x &lt; 0</FormulaBox>  <FormulaBox inline>x &gt; 2</FormulaBox>.</p>
            <p> : <FormulaBox inline>0 &lt; x &lt; 2</FormulaBox>.</p>
          </div>
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        <div className="space-y-6">
          {exercises.map((exercise) => (
            <Exercise key={exercise.id} exercise={exercise} />
          ))}
        </div>
      </section>

      {/* Quiz Section */}
      <Quiz questions={quizQuestions} />
    </LessonLayout>
  );
};

export default CalculusPolynomialLesson;
