import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const StatisticsDispersionLesson = () => {
  const title = " () -  ";
  const lessonId = "35381_statistics_dispersion";
  const nextLessonPath = "/lessons/probability-tree-conditional";

  const exercises = [
    {
      id: 'ex1',
      question: '  4   : 70, 75, 85, 90.      . (    )',
      correctAnswer: '7.91',
      tolerance: 0.01,
      solution: (
        <div>
          <p><strong> :</strong></p>
          <p>1. : <FormulaBox inline>{"\\bar{x} = \\frac{70+75+85+90}{4} = \\frac{320}{4} = 80"}</FormulaBox>.</p>
          <p>2.   :</p>
          <ul className="list-disc pr-4">
            <li><FormulaBox inline>(70-80)^2 = (-10)^2 = 100</FormulaBox></li>
            <li><FormulaBox inline>(75-80)^2 = (-5)^2 = 25</FormulaBox></li>
            <li><FormulaBox inline>(85-80)^2 = (5)^2 = 25</FormulaBox></li>
            <li><FormulaBox inline>(90-80)^2 = (10)^2 = 100</FormulaBox></li>
          </ul>
          <p>3.   : <FormulaBox inline>100+25+25+100 = 250</FormulaBox>.</p>
          <p>4. : <FormulaBox inline>S^2 = \\frac{250}{4} = 62.5</FormulaBox>.</p>
          <p>5.  : <FormulaBox inline>{"S = \\sqrt{62.5} \\approx 7.90569"}</FormulaBox>.</p>
          <p> : 7.91.</p>
        </div>
      )
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '     (,    80),    ?',
      options: [
        { value: 'a', label: '0' },
        { value: 'b', label: '1' },
        { value: 'c', label: '  ' }
      ],
      correctAnswer: 'a'
    },
    {
      id: 'q2',
      question: '  A: (10, 20, 30).   B: (10, 15, 20, 25, 30).        ?',
      options: [
        { value: 'a', label: ' A' },
        { value: 'b', label: ' B' },
        { value: 'c', label: '   ' }
      ],
      correctAnswer: 'a'
    }
  ];

  return (
    <LessonLayout title={title} lessonId={lessonId} nextLessonPath={nextLessonPath}>
      {/* Learn Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2"> </h2>
        <div className="space-y-4 text-gray-700 leading-relaxed">
          <p>      ,  ,  .       "" ,            .</p>
          <p> ,   <strong> </strong>.          .     ,    .    ,      .</p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">1. </h3>
          <p>     .            .</p>
          <div className="my-2 text-center">
            <FormulaBox>{"\\text{Range} = \\text{Maximum Value} - \\text{Minimum Value}"}</FormulaBox>
          </div>
          <p>,     70, 80, 85, 90, 100,   <FormulaBox inline>100 - 70 = 30</FormulaBox>.</p>
          <p className="text-sm text-gray-600">  ,            .</p>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">2.  (Variance)</h3>
          <p>           .     -<FormulaBox inline>S^2</FormulaBox> ( )  <FormulaBox inline>\\sigma^2</FormulaBox> ( ).</p>
          <p>   :</p>
          <div className="my-2 text-center">
            <FormulaBox>{"S^2 = \\frac{\\sum_{i=1}^{N} (x_i - \\bar{x})^2}{N}"}</FormulaBox>
          </div>
          <p>:</p>
          <ul className="list-disc pr-6 space-y-1">
            <li><FormulaBox inline>x_i</FormulaBox>      .</li>
            <li><FormulaBox inline>{"\\bar{x}"}</FormulaBox>   .</li>
            <li><FormulaBox inline>N</FormulaBox>    .</li>
            <li><FormulaBox inline>{"\\sum"}</FormulaBox>  .</li>
          </ul>
          <p> : 1.    (<FormulaBox inline>{"\\bar{x}"}</FormulaBox>). 2.  ,     (<FormulaBox inline>{"x_i - \\bar{x}"}</FormulaBox>). 3.     (<FormulaBox inline>{"(x_i - \\bar{x})^2"}</FormulaBox>). 4.     . 5.      (<FormulaBox inline>N</FormulaBox>).</p>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">3.   (Standard Deviation)</h3>
          <p>       .      ,  -<FormulaBox inline>S</FormulaBox>  <FormulaBox inline>\\sigma</FormulaBox>.</p>          <div className="my-2 text-center">
            <FormulaBox>{"S = \\sqrt{S^2} = \\sqrt{\\frac{\\sum_{i=1}^{N} (x_i - \\bar{x})^2}{N}}"}</FormulaBox>
          </div>
          <p>            ( ,   ).           ,           .</p>

          <h3 className="text-xl font-semibold mt-8 mb-2 text-gray-800"> :     </h3>
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 mb-6">
            <p className="font-medium"><strong>:</strong>        : 6, 7, 8, 9, 10.</p>
            <p><strong>:</strong></p>
              <p><strong> 1:   (<FormulaBox inline>{"\\bar{x}"}</FormulaBox>)</strong></p>
            <div className="my-2 text-center">
              <FormulaBox>{"\\bar{x} = \\frac{6+7+8+9+10}{5} = \\frac{40}{5} = 8"}</FormulaBox>
            </div>
            
            <p><strong> 2:     </strong></p>
            <div className="overflow-x-auto">
              <table className="w-full border-collapse border border-gray-300 mt-4 mb-4">
                <thead>
                  <tr className="bg-gray-50">
                    <th className="border border-gray-300 p-3 text-center font-semibold"> (<FormulaBox inline>x_i</FormulaBox>)</th>                    <th className="border border-gray-300 p-3 text-center font-semibold">  (<FormulaBox inline>{"x_i - \\bar{x}"}</FormulaBox>)</th>
                    <th className="border border-gray-300 p-3 text-center font-semibold">  (<FormulaBox inline>{"(x_i - \\bar{x})^2"}</FormulaBox>)</th>
                  </tr>
                </thead>
                <tbody>
                  <tr><td className="border border-gray-300 p-3 text-center">6</td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>6-8 = -2</FormulaBox></td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>(-2)^2 = 4</FormulaBox></td></tr>
                  <tr><td className="border border-gray-300 p-3 text-center">7</td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>7-8 = -1</FormulaBox></td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>(-1)^2 = 1</FormulaBox></td></tr>
                  <tr><td className="border border-gray-300 p-3 text-center">8</td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>8-8 = 0</FormulaBox></td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>(0)^2 = 0</FormulaBox></td></tr>
                  <tr><td className="border border-gray-300 p-3 text-center">9</td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>9-8 = 1</FormulaBox></td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>(1)^2 = 1</FormulaBox></td></tr>
                  <tr><td className="border border-gray-300 p-3 text-center">10</td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>10-8 = 2</FormulaBox></td><td className="border border-gray-300 p-3 text-center"><FormulaBox inline>(2)^2 = 4</FormulaBox></td></tr>
                  <tr className="bg-gray-100"><td className="border border-gray-300 p-3 text-center font-bold" colSpan="2"><strong>   (<FormulaBox inline>{"\\sum (x_i - \\bar{x})^2"}</FormulaBox>)</strong></td><td className="border border-gray-300 p-3 text-center font-bold"><strong>10</strong></td></tr>
                </tbody>
              </table>
            </div>
            
            <p><strong> 3:   (<FormulaBox inline>S^2</FormulaBox>)</strong></p>
            <p>  <FormulaBox inline>N=5</FormulaBox>.</p>
            <div className="my-2 text-center">
              <FormulaBox>{"S^2 = \\frac{\\sum (x_i - \\bar{x})^2}{N} = \\frac{10}{5} = 2"}</FormulaBox>
            </div>
            
            <p><strong> 4:    (<FormulaBox inline>S</FormulaBox>)</strong></p>            <div className="my-2 text-center">
              <FormulaBox>{"S = \\sqrt{S^2} = \\sqrt{2} \\approx 1.414"}</FormulaBox>
            </div>
            <p>:      -1.414.</p>
          </div>
          <p className="text-sm text-gray-600">:      ,          ,     ( N).</p>
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2"> </h2>
        <div className="space-y-6">
          {exercises.map((exercise, index) => (
            <Exercise
              key={exercise.id}
              id={exercise.id}
              question={` ${index + 1}: ${exercise.question}`}
              correctAnswer={exercise.correctAnswer}
              tolerance={exercise.tolerance}
              solution={exercise.solution}
            />
          ))}
        </div>
      </section>

      {/* Quiz Section */}      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">   </h2>
        <Quiz questions={quizQuestions} />
      </section>
    </LessonLayout>
  );
};

export default StatisticsDispersionLesson;
