import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const AlgebraLinearEquationsTwoVariablesLesson = () => {
  const SystemOfEquations = ({ children }) => (
    <div className="my-4 text-center">
      <FormulaBox>{children}</FormulaBox>
    </div>
  );

  const checkSystemAnswer = (userAnswer, correctAnswer) => {
    // Normalize answers by removing parentheses and spaces
    const normalize = (answer) => answer.replace(/[()]/g, '').replace(/\s+/g, '').trim();
    return normalize(userAnswer) === normalize(correctAnswer);
  };

  return (
    <LessonLayout 
      title="      " 
      lessonId="35182-algebra-linear-equations-two-variables"
      nextLessonPath="/lessons/algebra-quadratic-equations"
    >
      {/* Learning Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        
        <div className="space-y-6 text-gray-700 leading-relaxed">
          <p>
                   (  <FormulaBox inline>x</FormulaBox> -<FormulaBox inline>y</FormulaBox>) 
                      .      .
          </p>
          
          <p>     :</p>
          
          <SystemOfEquations>
            \begin{"{"}cases{"}"} 
            a_1x + b_1y = c_1 \\ 
            a_2x + b_2y = c_2 
            \end{"{"}cases{"}"}
          </SystemOfEquations>
          
          <p>
             <FormulaBox inline>a_1, b_1, c_1, a_2, b_2, c_2</FormulaBox>   . 
                 <FormulaBox inline>(x,y)</FormulaBox>    .
          </p>

          <h3 className="text-xl font-semibold mt-6 mb-2"> :</h3>
          <p>      :</p>

          <div className="bg-blue-50 border-r-4 border-blue-400 p-4 mb-4">
            <h4 className="text-lg font-semibold mb-2">1.  </h4>
            <ol className="list-decimal list-inside space-y-2 pr-5">
              <li>    (, <FormulaBox inline>y</FormulaBox>)  .</li>
              <li>        .</li>
              <li>    (     ).</li>
              <li>       (    )       .</li>
            </ol>
          </div>

          <div className="bg-green-50 border-r-4 border-green-400 p-4 mb-4">
            <h4 className="text-lg font-semibold mb-2">2.    ( / )</h4>
            <ol className="list-decimal list-inside space-y-2 pr-5">
              <li>  ,     (  )              (  )  .</li>
              <li> (  )   (  )    .  ,   "".</li>
              <li>    (  ).</li>
              <li>             .</li>
            </ol>
          </div>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50">
            <h4 className="text-lg font-semibold mb-2">  (     ):</h4>
            <p className="font-medium">:    2    .    21 .     ?</p>
            
            <p className="mt-2">: <FormulaBox inline>g</FormulaBox> -    , <FormulaBox inline>r</FormulaBox> -    .</p>
            <p>  :</p>
            
            <SystemOfEquations>
              \begin{"{"}cases{"}"} 
              g = 2r \\ 
              g + r = 21 
              \end{"{"}cases{"}"}
            </SystemOfEquations>

            <p className="mt-2"><strong>  :</strong></p>
            <div className="space-y-2 mt-2">
              <p>1.      <FormulaBox inline>g</FormulaBox> : <FormulaBox inline>g = 2r</FormulaBox>.</p>
              <p>2.   <FormulaBox inline>2r</FormulaBox>  <FormulaBox inline>g</FormulaBox>  : <FormulaBox inline>(2r) + r = 21</FormulaBox>.</p>
              <p>3.    : <FormulaBox inline>3r = 21 \Rightarrow r = \frac{21}{3} \Rightarrow r = 7</FormulaBox>.</p>
              <p>4.  <FormulaBox inline>r=7</FormulaBox>  : <FormulaBox inline>g = 2 \cdot 7 \Rightarrow g = 14</FormulaBox>.</p>
            </div>
            
            <p className="mt-2">
              ,   7    14 .   <FormulaBox inline>(g,r) = (14,7)</FormulaBox> 
              ( <FormulaBox inline>(x,y) = (14,7)</FormulaBox>    -x -y).
            </p>
          </div>
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        
        <div className="space-y-8">
          <Exercise
            question={
              <div>
                <p> 1:     :</p>
                <SystemOfEquations>
                  \begin{"{"}cases{"}"} 
                  x + y = 10 \\ 
                  x - y = 4 
                  \end{"{"}cases{"}"}
                </SystemOfEquations>
                <p>     (x,y), : (7,3).</p>
              </div>
            }
            answer="(7,3)"
            type="text"
            checkCustom={(userAnswer) => checkSystemAnswer(userAnswer, "7,3")}
            solution={
              <div>
                <p><strong>  (  ):</strong></p>
                <p>   :</p>
                <FormulaBox>(x+y) + (x-y) = 10 + 4</FormulaBox>
                <FormulaBox>2x = 14 \Rightarrow x = 7</FormulaBox>
                <p> <FormulaBox inline>x=7</FormulaBox>  : <FormulaBox inline>7 + y = 10 \Rightarrow y = 3</FormulaBox>.</p>
                <p> : <FormulaBox inline>(7,3)</FormulaBox>.</p>
              </div>
            }
          />

          <Exercise
            question={
              <div>
                <p> 2:     :</p>
                <SystemOfEquations>
                  \begin{"{"}cases{"}"} 
                  2x + 3y = 12 \\ 
                  x - y = 1 
                  \end{"{"}cases{"}"}
                </SystemOfEquations>
                <p>     (x,y).</p>
              </div>
            }
            answer="(3,2)"
            type="text"
            checkCustom={(userAnswer) => checkSystemAnswer(userAnswer, "3,2")}
            solution={
              <div>
                <p><strong>  ( ):</strong></p>
                <p>    <FormulaBox inline>x</FormulaBox>: <FormulaBox inline>x = y + 1</FormulaBox>.</p>
                <p>   : <FormulaBox inline>2(y+1) + 3y = 12</FormulaBox>.</p>
                <FormulaBox>2y + 2 + 3y = 12</FormulaBox>
                <FormulaBox>5y + 2 = 12 \Rightarrow 5y = 10 \Rightarrow y = 2</FormulaBox>
                <p> <FormulaBox inline>y=2</FormulaBox>  : <FormulaBox inline>x = 2 + 1 = 3</FormulaBox>.</p>
                <p> : <FormulaBox inline>(3,2)</FormulaBox>.</p>
              </div>
            }
          />
        </div>
      </section>

      {/* Quiz Section */}
      <Quiz
        title="   "
        questions={[
          {
            id: 1,
            question: (
              <div>
                <p>  <FormulaBox inline>x</FormulaBox>    ?</p>
                <SystemOfEquations>
                  \begin{"{"}cases{"}"} 
                  x + y = 5 \\ 
                  x - y = 1 
                  \end{"{"}cases{"}"}
                </SystemOfEquations>
              </div>
            ),
            options: [
              { value: "a", label: "1" },
              { value: "b", label: "2" },
              { value: "c", label: "3" },
              { value: "d", label: "4" }
            ],
            correctAnswer: "c",
            explanation: " : (x+y) + (x-y) = 5 + 1  2x = 6  x = 3"
          },
          {
            id: 2,
            question: (
              <div>
                <p>  <FormulaBox inline>y</FormulaBox>    ?</p>
                <SystemOfEquations>
                  \begin{"{"}cases{"}"} 
                  y = 2x \\ 
                  x + y = 9 
                  \end{"{"}cases{"}"}
                </SystemOfEquations>
              </div>
            ),
            options: [
              { value: "a", label: "2" },
              { value: "b", label: "3" },
              { value: "c", label: "6" },
              { value: "d", label: "9" }
            ],
            correctAnswer: "c",
            explanation: ": x + 2x = 9  3x = 9  x = 3.  y = 2  3 = 6"
          }
        ]}        />
    </LessonLayout>
  );
};

export default AlgebraLinearEquationsTwoVariablesLesson;
