import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const CalculusRationalLesson = () => {
  const exercises = [
    {
      id: 'ex1',
      question: '     ( )  ',
      formula: 'f(x) = \\frac{3x}{x^2-4}',
      fields: [
        { id: 'domain', label: ' ', placeholder: ': x1, x-2' },
        { id: 'vertical', label: ' ', placeholder: ': x=1, x=-2 ( ,  "")' },
        { id: 'horizontal', label: ' ', placeholder: ': y=0 ( ,  "")' }
      ],
      solution: (
        <div>
          <p><strong> :</strong></p>
          <p>: <FormulaBox inline>{`f(x) = \\frac{3x}{x^2-4}`}</FormulaBox>.</p>
          <p>1. <strong> :</strong>   . <FormulaBox inline>{`x^2-4 \\neq 0 \\Rightarrow x^2 \\neq 4 \\Rightarrow x \\neq \\pm 2`}</FormulaBox>.</p>
          <p> : <FormulaBox inline>{`x \\neq 2`}</FormulaBox>  <FormulaBox inline>{`x \\neq -2`}</FormulaBox>.</p>
          <p>2. <strong> :</strong>    : <FormulaBox inline>{`x=2, x=-2`}</FormulaBox>.</p>
          <p> <FormulaBox inline>{`3x`}</FormulaBox>      (<FormulaBox inline>{`3(2)=6 \\neq 0`}</FormulaBox>, <FormulaBox inline>{`3(-2)=-6 \\neq 0`}</FormulaBox>).</p>
          <p>,    <FormulaBox inline>{`x=2`}</FormulaBox> -<FormulaBox inline>{`x=-2`}</FormulaBox>.</p>
          <p>3. <strong> :</strong>     1 (<FormulaBox inline>{`3x^1`}</FormulaBox>).     2 (<FormulaBox inline>{`x^2`}</FormulaBox>).</p>
          <p>   (1)    (2),    <FormulaBox inline>{`y=0`}</FormulaBox>.</p>
          <p><strong>:</strong>  : <FormulaBox inline>{`x \\neq 2, x \\neq -2`}</FormulaBox>.  : <FormulaBox inline>{`x=2, x=-2`}</FormulaBox>.  : <FormulaBox inline>{`y=0`}</FormulaBox>.</p>
        </div>
      ),
      checkAnswer: (answers) => {
        const domain = answers.domain?.toLowerCase().trim();
        const vertical = answers.vertical?.toLowerCase().trim();
        const horizontal = answers.horizontal?.toLowerCase().trim();
        
        const domainCorrect = domain.includes('x2') || domain.includes('x-2') || domain.includes('x!=2') || domain.includes('x!=-2');
        const verticalCorrect = (vertical.includes('x=2') && vertical.includes('x=-2')) || (vertical.includes('x=-2') && vertical.includes('x=2'));
        const horizontalCorrect = horizontal.includes('y=0') || horizontal.includes('y = 0');
        
        if (domainCorrect && verticalCorrect && horizontalCorrect) {
          return { correct: true, message: ' !   .' };
        } else {
          let feedback = ' . ';
          if (!domainCorrect) feedback += ' :     . ';
          if (!verticalCorrect) feedback += ' :       . ';
          if (!horizontalCorrect) feedback += ' :    . ';
          return { correct: false, message: feedback };
        }
      }
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '    ',
      formula: 'f(x) = \\frac{5}{x-7}',
      options: [
        { value: 'a', label: ' x' },
        { value: 'b', label: 'x  7' },
        { value: 'c', label: 'x  5' }
      ],
      correct: 'b',
      explanation: '        . x-70  x7.'
    },
    {
      id: 'q2',
      question: '    ',
      formula: 'f(x) = \\frac{6x^2+1}{2x^2-x}',
      options: [
        { value: 'a', label: 'y=0' },
        { value: 'b', label: 'y=3' },
        { value: 'c', label: '  ' }
      ],
      correct: 'b',
      explanation: '     (x),      : 6/2 = 3,  y=3.'
    }
  ];

  return (
    <LessonLayout
      title="  ()"
      breadcrumbs={[
        { label: ' ', href: '/' },
        { label: ' 35382', href: '/lessons' },
        { label: '   ()' },
        { label: ' ' }
      ]}
    >
      {/* Learn Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
          :   
        </h2>
        <div className="space-y-4 text-gray-700 leading-relaxed">          <p>
                 <FormulaBox inline>{`f(x) = \\frac{P(x)}{Q(x)}`}</FormulaBox>, 
             <FormulaBox inline>{`P(x)`}</FormulaBox> -<FormulaBox inline>{`Q(x)`}</FormulaBox>  , 
            -<FormulaBox inline>{`Q(x) \\neq 0`}</FormulaBox>.
          </p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">1.  </h3>
          <p>
                     : <FormulaBox inline>{`Q(x) \\neq 0`}</FormulaBox>
          </p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">2.  </h3>
          <p>
                 <FormulaBox inline>{`Q(x) = 0`}</FormulaBox>  <FormulaBox inline>{`P(x) \\neq 0`}</FormulaBox>
          </p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">3.  </h3>
          <ul className="space-y-2 mr-4">
            <li>      : <FormulaBox inline>{`y = 0`}</FormulaBox></li>
            <li>   : <FormulaBox inline>{`y = \\frac{a}{b}`}</FormulaBox> (  )</li>
            <li>    :   </li>
          </ul>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">4.    </h3>
          <p>
              :
          </p>
          <FormulaBox>{`\\left(\\frac{f}{g}\\right)' = \\frac{f'g - fg'}{g^2}`}</FormulaBox>
          
          <div className="bg-blue-50 p-4 rounded-lg border-r-4 border-blue-400">
            <p className="font-medium">:   <FormulaBox inline>{`f(x) = \\frac{2x+1}{x-3}`}</FormulaBox></p>
            <p>: <FormulaBox inline>{`u = 2x+1`}</FormulaBox>, <FormulaBox inline>{`v = x-3`}</FormulaBox></p>
            <p><FormulaBox inline>{`u' = 2`}</FormulaBox>, <FormulaBox inline>{`v' = 1`}</FormulaBox></p>
            <p>:</p>
            <FormulaBox>{`f'(x) = \\frac{2(x-3) - (2x+1) \\cdot 1}{(x-3)^2} = \\frac{2x-6-2x-1}{(x-3)^2} = \\frac{-7}{(x-3)^2}`}</FormulaBox>
          </div>
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-green-600 mb-4 border-b-2 border-green-200 pb-2">
          
        </h2>
        {exercises.map((exercise) => (
          <Exercise key={exercise.id} {...exercise} />
        ))}
      </section>

      {/* Quiz Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-orange-600 mb-4 border-b-2 border-orange-200 pb-2">
          
        </h2>
        <Quiz questions={quizQuestions} />
      </section>
    </LessonLayout>
  );
};

export default CalculusRationalLesson;
