import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';


const CalculusSquareRootLesson = () => {
  const lessonId = 'calculus_square_root_he';
  const nextLessonPath = '/calculus-rational';

  const exercises = [
    {
      id: 'ex1',
      question: '      f(x) = (9-x).',
      correctAnswer: '-3<=x<=3',
      isDomain: true,
      placeholder: ': x>=0  -1<=x<=1',
      solution: (
        <div>
          <p><strong> :</strong></p>
          <p>     -:</p>
          <FormulaBox>{`9-x^2 \\ge 0`}</FormulaBox>
          <p>  "" ( <FormulaBox inline>{`x^2`}</FormulaBox> ).   :</p>
          <FormulaBox>{`9-x^2 = 0 \\Rightarrow x^2 = 9 \\Rightarrow x = \\pm 3`}</FormulaBox>
          <p>       ().</p>
          <p><strong>:</strong>    <FormulaBox inline>{`-3 \\le x \\le 3`}</FormulaBox>.</p>
        </div>
      )
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '     f(x) = (x+5)?',
      options: [
        { value: 'a', label: <FormulaBox inline>{`x \\ge 5`}</FormulaBox> },
        { value: 'b', label: <FormulaBox inline>{`x \\ge -5`}</FormulaBox> },
        { value: 'c', label: <FormulaBox inline>{`x > -5`}</FormulaBox> }
      ],
      correctAnswer: 'b'
    },
    {
      id: 'q2',
      question: '   f(x) = (3x)?',
      options: [
        { value: 'a', label: <FormulaBox inline>{`\\frac{3}{2\\sqrt{3x}}`}</FormulaBox> },
        { value: 'b', label: <FormulaBox inline>{`\\frac{1}{2\\sqrt{3x}}`}</FormulaBox> },
        { value: 'c', label: <FormulaBox inline>{`2\\sqrt{3x}`}</FormulaBox> }
      ],
      correctAnswer: 'a'
    }
  ];

  return (
    <LessonLayout 
      title="  () "
      lessonId={lessonId}
      nextLessonPath={nextLessonPath}
    >
      {/* Learning Section */}
      <section id="learn" aria-labelledby="learn-heading" className="mb-12">
        <h2 id="learn-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
          :    
        </h2>
        <div className="space-y-4 text-gray-700 leading-relaxed">
          <p>         ,  <FormulaBox inline>{`f(x) = \\sqrt{ax+b}`}</FormulaBox>  <FormulaBox inline>{`f(x) = \\sqrt{ax^2+bx+c}`}</FormulaBox>,      .</p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">1.  </h3>
          <p>       <strong>     - (    )</strong>.</p>
          <FormulaBox>{`\\text{If } f(x) = \\sqrt{g(x)}, \\text{ then domain is } g(x) \\ge 0`}</FormulaBox>
          <p>,        - <FormulaBox inline>{`g(x) \\ge 0`}</FormulaBox>.</p>
          
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-4">
            <p className="font-medium"><strong> ( ):</strong>       <FormulaBox inline>{`f(x) = \\sqrt{x-2}`}</FormulaBox>.</p>
            <p><strong>:</strong></p>
            <p>     -:</p>
            <FormulaBox>{`x-2 \\ge 0`}</FormulaBox>
            <FormulaBox>{`x \\ge 2`}</FormulaBox>
            <p>   <FormulaBox inline>{`x \\ge 2`}</FormulaBox>.</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">2.    </h3>
          <p>     <FormulaBox inline>{`f(x) = \\sqrt{u(x)}`}</FormulaBox> ( <FormulaBox inline>{`u(x)`}</FormulaBox>   ) :</p>
          <FormulaBox>{`f'(x) = (\\sqrt{u(x)})' = \\frac{u'(x)}{2\\sqrt{u(x)}}`}</FormulaBox>
          <p>               ,   <FormulaBox inline>{`2\\sqrt{u(x)}`}</FormulaBox>    (, <FormulaBox inline>{`u(x) > 0`}</FormulaBox>).</p>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-4">
            <p className="font-medium"><strong> ( ):</strong>    <FormulaBox inline>{`f(x) = \\sqrt{2x+3}`}</FormulaBox>.</p>
            <p><strong>:</strong></p>
            <p> <FormulaBox inline>{`u(x) = 2x+3`}</FormulaBox>. , <FormulaBox inline>{`u'(x) = 2`}</FormulaBox>.</p>
            <p>  :</p>
            <FormulaBox>{`f'(x) = \\frac{u'(x)}{2\\sqrt{u(x)}} = \\frac{2}{2\\sqrt{2x+3}} = \\frac{1}{\\sqrt{2x+3}}`}</FormulaBox>
            <p>     <FormulaBox inline>{`2x+3 \\ge 0 \\Rightarrow x \\ge -1.5`}</FormulaBox>.</p>
            <p>     <FormulaBox inline>{`2x+3 > 0 \\Rightarrow x > -1.5`}</FormulaBox>.</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">3.   </h3>
          <p> :</p>
          <ul className="list-disc pr-6 space-y-1">
            <li>  </li>
            <li> </li>
            <li>   </li>
            <li>   </li>
            <li>  </li>
            <li> </li>
          </ul>

          <div className="border border-blue-200 rounded-lg p-4 bg-blue-50 space-y-3 my-4">
            <p className="font-medium text-blue-800"><strong> :</strong>    <FormulaBox inline>{`f(x) = \\sqrt{x}`}</FormulaBox>.</p>
            <p><strong>:</strong></p>
            <p>1. <strong> :</strong> <FormulaBox inline>{`x \\ge 0`}</FormulaBox>.</p>
            <p>2. <strong>:</strong> <FormulaBox inline>{`f'(x) = \\frac{1}{2\\sqrt{x}}`}</FormulaBox>  <FormulaBox inline>{`x > 0`}</FormulaBox>.</p>
            <p>3. <strong>  :</strong></p>
            <ul className="list-disc pr-4">
              <li><FormulaBox inline>{`f'(x) > 0`}</FormulaBox>  <FormulaBox inline>{`x > 0`}</FormulaBox> -  </li>
              <li>   </li>
            </ul>
            <p>4. <strong> :</strong></p>
            <ul className="list-disc pr-4">
              <li> <FormulaBox inline>{`x \\to 0^+`}</FormulaBox>: <FormulaBox inline>{`f(x) \\to 0`}</FormulaBox></li>
              <li> <FormulaBox inline>{`x \\to +\\infty`}</FormulaBox>: <FormulaBox inline>{`f(x) \\to +\\infty`}</FormulaBox></li>
            </ul>
            <p> <FormulaBox inline>{`(0,0)`}</FormulaBox>   .     ,     .</p>
          </div>
        </div>
      </section>

      {/* Practice Section */}
      <section id="practice" aria-labelledby="practice-heading" className="mb-12">
        <h2 id="practice-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        <div className="space-y-6">
          {exercises.map((exercise) => (
            <Exercise key={exercise.id} {...exercise} />
          ))}
        </div>
      </section>

      {/* Quiz Section */}
      <section id="quiz" aria-labelledby="quiz-heading" className="mb-12">
        <h2 id="quiz-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
             
        </h2>
        <Quiz questions={quizQuestions} lessonId={lessonId} />
      </section>
    </LessonLayout>
  );
};

export default CalculusSquareRootLesson;
