import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const NormalDistributionLesson = () => {
  const exercises = [
    {
      id: 'ex1',
      question: '           500     40 .         -450 ? (    -4   )',
      correctAnswer: '0.1056',
      tolerance: 0.01,
      solution: (
        <div>
          <p><strong> :</strong></p>          <p>: <FormulaBox inline>\\mu = 500, \\sigma = 40</FormulaBox></p>
          <p> <FormulaBox inline>X = 450</FormulaBox> :</p>
          <FormulaBox>Z = \\frac{450 - 500}{40} = \\frac{-50}{40} = -1.25</FormulaBox>          <p>  Z  <FormulaBox inline>P(Z &lt; -1.25)</FormulaBox>.  , <FormulaBox inline>P(Z &lt; -1.25) = P(Z &gt; 1.25) = 1 - P(Z &lt; 1.25)</FormulaBox>.</p>
          <p>, <FormulaBox inline>P(Z &lt; 1.25) \\approx 0.8944</FormulaBox>.</p>
          <p>, <FormulaBox inline>P(Z &lt; -1.25) \\approx 1 - 0.8944 = 0.1056</FormulaBox>.</p>
          <p>  -10.56%.</p>
        </div>
      )
    },
    {
      id: 'ex2',
      question: '      70   10.   -Z    85?',
      correctAnswer: '1.5',
      tolerance: 0.01,
      solution: (        <div>
          <p><strong>:</strong></p>
          <p>: <FormulaBox inline>X = 85, \\mu = 70, \\sigma = 10</FormulaBox></p>
          <FormulaBox>Z = \\frac{85 - 70}{10} = \\frac{15}{10} = 1.5</FormulaBox>
          <p> -Z    1.5.       -1.5   .</p>
        </div>
      )
    },
    {
      id: 'ex3',
      question: ' ,        200     5 .        195 -205 ? (   -4   )',
      correctAnswer: '0.6826',
      tolerance: 0.01,
      solution: (
        <div>          <p><strong>:</strong></p>
          <p>: <FormulaBox inline>\\mu = 200, \\sigma = 5</FormulaBox></p>
          <p> 195 : <FormulaBox inline>Z_1 = \\frac{195 - 200}{5} = -1</FormulaBox></p>
          <p> 205 : <FormulaBox inline>Z_2 = \\frac{205 - 200}{5} = 1</FormulaBox></p>
          <p>  <FormulaBox inline>P(-1 &lt; Z &lt; 1)</FormulaBox>.</p>
          <p>   (68-95-99.7), -68%        .</p>
          <p>   0.6826 (68.26%).</p>
        </div>
      )
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '   Z  ?',
      options: [
        '   ',
        '   ',
        '   ',
        '  '
      ],
      correctAnswer: '   ',
      explanation: ' Z        ,    Z = (X - )/.'
    },
    {
      id: 'q2',
      question: '   (68-95-99.7),          ?',
      options: ['68%', '95%', '99.7%', '50%'],
      correctAnswer: '95%',
      explanation: '  , 95%         (  2).'
    },
    {
      id: 'q3',
      question: '     ?',
      options: [
        ' = 1,   = 0',
        ' = 0,   = 1',
        ' = 100,   = 15',
        ' = 50,   = 10'
      ],
      correctAnswer: ' = 0,   = 1',
      explanation: '   (Z)    0   1,    .'
    }
  ];

  return (
    <LessonLayout
      title=" "
      lessonId="35381-normal-distribution"
      nextLessonPath="/lesson/35382-functions-parabola-investigation"
    >
      {/* Learn Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2"> </h2>
        <div className="space-y-4 text-gray-700 leading-relaxed">
          <p>         .        .</p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">  </h3>
          <ul className="list-disc pr-6 space-y-1">
            <li><strong>:</strong>     ().</li>
            <li><strong> :</strong>     ,     .</li>
            <li><strong>,   :</strong>  = Md = Mo.</li>
            <li><strong>     -1 ( 100%):</strong>    .</li>
            <li><strong>    :</strong>  ()   ().</li>
          </ul>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">   (Z)</h3>
          <p>   ,     ,    0    1. , <FormulaBox inline>Z \\sim N(0, 1)</FormulaBox>.</p>
          <p>     <FormulaBox inline>X</FormulaBox>     <FormulaBox inline>\\mu</FormulaBox>   <FormulaBox inline>\\sigma</FormulaBox>   <FormulaBox inline>Z</FormulaBox>  :</p>
          <FormulaBox>{"Z = \\frac{X - \\mu}{\\sigma}"}</FormulaBox>
          <p> ,       ( Z)   .</p>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800"> 68-95-99.7 ( )</h3>
          <p> :</p>
          <ul className="list-disc pr-6 space-y-1">
            <li>-<strong>68%</strong>         ( <FormulaBox inline>\\mu - \\sigma</FormulaBox> - <FormulaBox inline>\\mu + \\sigma</FormulaBox>).</li>
            <li>-<strong>95%</strong>         ( <FormulaBox inline>\\mu - 2\\sigma</FormulaBox> - <FormulaBox inline>\\mu + 2\\sigma</FormulaBox>).</li>
            <li>-<strong>99.7%</strong>         ( <FormulaBox inline>\\mu - 3\\sigma</FormulaBox> - <FormulaBox inline>\\mu + 3\\sigma</FormulaBox>).</li>
          </ul>

          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 my-6">
            <div className="text-center">
              <p className="font-medium mb-2">  (68-95-99.7)</p>
              <div className="bg-white rounded-lg p-3 inline-block">
                <FormulaBox>{"\\begin{array}{c} \\mu - 3\\sigma \\quad \\mu - 2\\sigma \\quad \\mu - \\sigma \\quad \\mu \\quad \\mu + \\sigma \\quad \\mu + 2\\sigma \\quad \\mu + 3\\sigma \\\\ \\underbrace{\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad}_{99.7\\%} \\\\ \\underbrace{\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad}_{95\\%} \\\\ \\underbrace{\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad\\quad}_{68\\%} \\end{array}"}</FormulaBox>
              </div>
            </div>
          </div>

          <h3 className="text-xl font-semibold mt-8 mb-2 text-gray-800"> :   Z</h3>
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 mb-6">
            <p className="font-medium"><strong>:</strong>       70   10.   -Z    85?</p>
            
            <p><strong>:</strong></p>
            <p>: <FormulaBox inline>X = 85, \\mu = 70, \\sigma = 10</FormulaBox></p>
            <FormulaBox>{"Z = \\frac{85 - 70}{10} = \\frac{15}{10} = 1.5"}</FormulaBox>
            <p> -Z    1.5.       -1.5   .</p>
          </div>

          <h3 className="text-xl font-semibold mt-8 mb-2 text-gray-800"> :   Z</h3>
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 mb-6">
            <p className="font-medium"><strong>:</strong>    Z     -1.5?</p>
            
            <p><strong>:</strong></p>
            <p>    .         Z ,  <FormulaBox inline>P(Z &lt; z)</FormulaBox>.</p>
            <p> <FormulaBox inline>z = 1.5</FormulaBox>,     .  , <FormulaBox inline>P(Z &lt; 1.5) \approx 0.9332</FormulaBox>.</p>
            <p>,   -93.32%.</p>
            
            <p><strong> :</strong></p>
            <p>   Z   -1.5?</p>            <FormulaBox>P(Z &gt; 1.5) = 1 - P(Z &lt; 1.5) = 1 - 0.9332 = 0.0668</FormulaBox>
            
            <p>   Z   0 -1.5?</p>
            <FormulaBox>P(0 &lt; Z &lt; 1.5) = P(Z &lt; 1.5) - P(Z &lt; 0)</FormulaBox>
            <p>    0, <FormulaBox inline>P(Z &lt; 0) = 0.5</FormulaBox>.</p>
            <FormulaBox>P(0 &lt; Z &lt; 1.5) = 0.9332 - 0.5 = 0.4332</FormulaBox>
          </div>
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2"> </h2>
        <div className="space-y-6">
          {exercises.map((exercise, index) => (
            <Exercise key={exercise.id} {...exercise} />
          ))}
        </div>
      </section>      {/* Quiz Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">  </h2>
        <Quiz questions={quizQuestions} />
      </section>
    </LessonLayout>
  );
};

export default NormalDistributionLesson;
