import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const P = 15; // Example value, replace with the correct one if needed

const AlgebraPercentagesLesson = () => {
  const DataTable = ({ children, className = "" }) => (
    <table className={`w-full border-collapse mt-4 mb-4 ${className}`}>
      {children}
    </table>
  );

  const TableHeader = ({ children }) => (
    <thead>
      <tr className="bg-gray-100">
        {children}
      </tr>
    </thead>
  );

  const TableCell = ({ children, isHeader = false, className = "" }) => (
    <td className={`border border-gray-300 p-3 text-right ${isHeader ? 'bg-gray-100 font-semibold' : ''} ${className}`}>
      {children}
    </td>
  );

  return (
    <LessonLayout 
      title="" 
      lessonId="35182-algebra-percentages"
      nextLessonPath="/lessons/algebra-inequalities"
    >
      {/* Learning Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        
        <div className="space-y-6 text-gray-700 leading-relaxed">
          <p>
                  ,    -100  . 
             ""  " ".     %.
          </p>
          
          <p>
            , 25%  25  100,  <FormulaBox inline>\frac{25}{100}</FormulaBox>, 
             -<FormulaBox inline>\frac{1}{4}</FormulaBox>  0.25.
          </p>

          <h3 className="text-xl font-semibold mt-6 mb-2">    </h3>
          <p>
              <FormulaBox inline>P\%</FormulaBox>  <FormulaBox inline>N</FormulaBox>, 
             :
          </p>
          
          <div className="my-4 text-center">
            <FormulaBox>{`\\text{Value} = \\\\frac{P}{100} \\\\cdot N`}</FormulaBox>
          </div>
          
          <p>  : <FormulaBox inline>{`\\text{Value} = (\\text{Decimal}) \\\\cdot N`}</FormulaBox>.</p>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50">
            <h4 className="text-lg font-semibold mb-2">:</h4>
            <p className="font-medium">:    20%  150.</p>
            <p className="mt-2"><strong>:</strong></p>
            <p> <FormulaBox inline>{`P=20`}</FormulaBox> -<FormulaBox inline>{`N=150`}</FormulaBox>.</p>
            <FormulaBox>{`\\text{Value} = \\\\frac{20}{100} \\\\cdot 150 = 0.20 \\\\cdot 150 = 30`}</FormulaBox>
            <p className="mt-2">, 20%  150  30.</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2">   (100%)</h3>
          <p>
                ( )   ,      (<FormulaBox inline>N</FormulaBox>).
          </p>
          <p>
             <FormulaBox inline>V</FormulaBox>     <FormulaBox inline>P\%</FormulaBox>  <FormulaBox inline>N</FormulaBox>, :
          </p>
          
          <div className="my-4 text-center">
            <FormulaBox>{`V = \\frac{P}{100} \\cdot N \\Rightarrow N = \\frac{V \\cdot 100}{P}`}</FormulaBox>
          </div>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50">
            <h4 className="text-lg font-semibold mb-2">:</h4>
            <p className="font-medium">: 40 "  10%  .   ?</p>
            <p className="mt-2"><strong>:</strong></p>
            <p> <FormulaBox inline>V=40</FormulaBox> -<FormulaBox inline>P=10</FormulaBox>.</p>
            <FormulaBox>{`N = \\frac{40 \\cdot 100}{10} = \\frac{4000}{10} = 400`}</FormulaBox>
            <p className="mt-2">   400 ".</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2">  </h3>
          <p>        .</p>
          
          <h4 className="text-lg font-semibold mt-4 mb-1">     </h4>
          <p>
                 ,      , 
                         . 
                 :
          </p>
          
          <ul className="list-disc list-inside space-y-1 pr-5">
            <li><strong> / </strong></li>
            <li><strong>  /  (100%)</strong></li>
            <li><strong>  (+% , -% )</strong></li>
            <li><strong>  (  )</strong></li>
            <li><strong>  / </strong></li>
          </ul>
          <p>      ,       .</p>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50">
            <h4 className="text-lg font-semibold mb-2">    :</h4>
            <p className="font-medium">:    200 ".   -15%.   ?</p>
            <p className="mt-2"><strong>  :</strong></p>
            
            <DataTable>
              <TableHeader>
                <TableCell isHeader></TableCell>
                <TableCell isHeader>  (")</TableCell>
                <TableCell isHeader> </TableCell>
                <TableCell isHeader>  (")</TableCell>
                <TableCell isHeader>  (")</TableCell>
              </TableHeader>
              <tbody>
                <tr>
                  <TableCell> </TableCell>
                  <TableCell>200 ( -100%)</TableCell>
                  <TableCell>+15% ()</TableCell>
                  <TableCell>?</TableCell>
                  <TableCell>?</TableCell>
                </tr>
              </tbody>
            </DataTable>

            <p className="mt-2"><strong>:</strong></p>
            <p>     200 ".</p>
            <p>1. <strong>  :</strong>   15%  .</p>
            <p className="text-center my-4">
              <FormulaBox>{"\\text{Change} = \\frac{15}{100} \\cdot 200 = 0.15 \\cdot 200 = 30"}</FormulaBox>
              <span className="text-lg mr-2"></span>
            </p>
            
            <p> :</p>
            <DataTable>
              <tbody>
                <tr>
                  <TableCell> </TableCell>
                  <TableCell>200</TableCell>
                  <TableCell>+15%</TableCell>
                  <TableCell className="font-bold">30</TableCell>
                  <TableCell>?</TableCell>
                </tr>
              </tbody>
            </DataTable>

            <p className="mt-2">2. <strong>  :</strong></p>
            <p>  =   +   ().</p>
            <p className="text-center my-4">
              <FormulaBox>{"\\text{New Price} = 200 + 30 = 230"}</FormulaBox>
              <span className="text-lg mr-2"></span>
            </p>

            <p>  :</p>
            <DataTable>
              <tbody>
                <tr>
                  <TableCell> </TableCell>
                  <TableCell>200</TableCell>
                  <TableCell>+15%</TableCell>
                  <TableCell>30</TableCell>
                  <TableCell className="font-bold">230</TableCell>
                </tr>
              </tbody>
            </DataTable>

            <p className="mt-2">     230 ".</p>
            
            <p className="mt-4"><strong>  (  ):</strong></p>
            <p>
                 -15%,     <FormulaBox inline>100\% + 15\% = 115\%</FormulaBox>  .
            </p>
            <p>  <FormulaBox inline>M_1</FormulaBox> :</p>
            <p className="text-center my-4">
              <FormulaBox>{`M_1 = \\frac{115}{100} \\cdot 200 = 1.15 \\cdot 200 = 230`}</FormulaBox>
              <span className="text-lg mr-2"></span>
            </p>
            <p>    : <FormulaBox inline>M_1 = M_0 \left(1 + \frac{P}{100}\right)</FormulaBox></p>
            <p className="text-center my-4">
              <FormulaBox>{`M_1 = 200 \\left(1 + \\frac{15}{100}\\right) = 200 \\cdot (1 + 0.15) = 200 \\cdot 1.15 = 230`}</FormulaBox>
              <span className="text-lg mr-2"></span>
            </p>
          </div>

          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 mt-4">
            <h4 className="text-lg font-semibold mb-2">   :</h4>
            <p className="font-medium">:    300 ".   -20%.   ?</p>
            <p className="mt-2"><strong> :</strong></p>
            
            <DataTable>
              <TableHeader>
                <TableCell isHeader></TableCell>
                <TableCell isHeader>  (")</TableCell>
                <TableCell isHeader> </TableCell>
                <TableCell isHeader>  (")</TableCell>
                <TableCell isHeader>  (")</TableCell>
              </TableHeader>
              <tbody>
                <tr>
                  <TableCell> </TableCell>
                  <TableCell>300</TableCell>
                  <TableCell>-20% ()</TableCell>
                  <TableCell>?</TableCell>
                  <TableCell>?</TableCell>
                </tr>
              </tbody>
            </DataTable>

            <p className="mt-2"><strong>:</strong></p>
            <p>1. <strong>  ():</strong> <FormulaBox inline>\frac{20}{100} \cdot 300 = 0.20 \cdot 300 = 60</FormulaBox> ".</p>
            <p>2. <strong> :</strong> <FormulaBox inline>300 - 60 = 240</FormulaBox> ".</p>
            <p className="mt-2">   240 ".</p>
            <p className="mt-2">
              <strong> :</strong>   -20%,    <FormulaBox inline>100\% - 20\% = 80\%</FormulaBox> .
            </p>
            <p className="text-center my-4">
              <FormulaBox>{`M_1 = \\frac{80}{100} \\cdot 300 = 0.80 \\cdot 300 = 240`}</FormulaBox>
              <span className="text-lg mr-2"></span>
            </p>
          </div>
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        
        <div className="space-y-8">
          <Exercise
            question=" 1:   30%  250?"
            answer="75"
            type="number"
            solution={
              <div>
                <p><strong> :</strong></p>
                <FormulaBox>{`\\frac{30}{100} \\cdot 250 = 0.30 \\cdot 250 = 75`}</FormulaBox>
                <p>  75.</p>
              </div>
            }
          />

          <Exercise
            question=" 2:    120 .   -25%.   ?"
            answer="90"
            type="number"
            hint="      ."
            solution={
              <div>
                <p><strong> :</strong></p>
                <p> :</p>
                <DataTable className="text-sm">
                  <TableHeader>
                    <TableCell isHeader></TableCell>
                    <TableCell isHeader> </TableCell>
                    <TableCell isHeader> </TableCell>
                    <TableCell isHeader> </TableCell>
                    <TableCell isHeader> </TableCell>
                  </TableHeader>
                  <tbody>
                    <tr>
                      <TableCell></TableCell>
                      <TableCell>120</TableCell>
                      <TableCell>-25%</TableCell>
                      <TableCell>30</TableCell>
                      <TableCell>90</TableCell>
                    </tr>
                  </tbody>
                </DataTable>
                <p> : <FormulaBox inline>\frac{25}{100} \cdot 120 = 0.25 \cdot 120 = 30</FormulaBox> ".</p>
                <p> : <FormulaBox inline>120 - 30 = 90</FormulaBox> ".</p>
                <p>: <FormulaBox inline>120 \left(1 - \frac{25}{100}\right) = 120 \cdot 0.75 = 90</FormulaBox> ".</p>
                <p>   90 ".</p>
              </div>
            }
          />
        </div>
      </section>

      {/* Quiz Section */}
      <Quiz
        title="   "
        questions={[
          {
            id: 1,
            question: " 30 . 60%  .   ?",
            options: [
              { value: "a", label: "12" },
              { value: "b", label: "18" },
              { value: "c", label: "10" },
              { value: "d", label: "15" }
            ],
            correctAnswer: "a",
            explanation: "60%  = 18 .   = 30 - 18 = 12 ."
          },
          {
            id: 2,
            question: "   -80  -100 .    ?",
            options: [
              { value: "a", label: "20%" },
              { value: "b", label: "25%" },
              { value: "c", label: "15%" },
              { value: "d", label: "30%" }
            ],
            correctAnswer: "b",
            explanation: "  100 - 80 = 20 .   = (20/80)  100% = 25%."
          }        ]}
      />
    </LessonLayout>
  );
};

export default AlgebraPercentagesLesson;
