import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const FunctionsParabolaInvestigationLesson = () => {
  const lessonId = 'functions_parabola_investigation_35381';
  const nextLessonPath = '/growth-decay';

  const exercises = [
    {
      id: 'ex1',
      question: '  y = -x + 6x - 5.     .',
      correctAnswer: '(3,4)',
      placeholder: '  (: (3,4))',
      solution: (
        <div>
          <p><strong> :</strong></p>
          <p> <FormulaBox inline>y = -x^2 + 6x - 5</FormulaBox>,  : <FormulaBox inline>a=-1, b=6, c=-5</FormulaBox>.</p>
          <p> -X  : <FormulaBox inline>{"x_k = -\\frac{b}{2a} = -\\frac{6}{2 \\cdot (-1)} = -\\frac{6}{-2} = 3"}</FormulaBox>.</p>
          <p> -Y  : <FormulaBox inline>y_k = -(3)^2 + 6(3) - 5 = -9 + 18 - 5 = 4</FormulaBox>.</p>
          <p>,    <FormulaBox inline>(3,4)</FormulaBox>.</p>
        </div>
      )
    },
    {
      id: 'ex2',
      question: '  y = 2x - 4x + 3.       Y.',
      correctAnswer: '(0,3)',
      placeholder: '  (: (0,3))',
      solution: (
        <div>
          <p><strong>:</strong></p>
          <p>   Y   <FormulaBox inline>x = 0</FormulaBox>.</p>
          <p> <FormulaBox inline>x = 0</FormulaBox> :</p>
          <p><FormulaBox inline>y = 2(0)^2 - 4(0) + 3 = 0 - 0 + 3 = 3</FormulaBox></p>
          <p>,     Y  <FormulaBox inline>(0,3)</FormulaBox>.</p>
        </div>
      )
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: ' y = 2x - 8x + 1,        ?',
      options: [
        { value: 'min', label: '' },
        { value: 'max', label: '' }
      ],
      correctAnswer: 'min'
    },
    {
      id: 'q2',
      question: '     -Y   y = x + 3x - 7?',
      options: [
        { value: 'a', label: '(0,7)' },
        { value: 'b', label: '(0,-7)' },
        { value: 'c', label: '(7,0)' },
        { value: 'd', label: '(-7,0)' }
      ],
      correctAnswer: 'b'
    },
    {
      id: 'q3',
      question: '     < 0    a > 0?',
      options: [
        { value: 'a', label: '     X' },
        { value: 'b', label: '     X' },
        { value: 'c', label: '     X' },
        { value: 'd', label: '  ' }
      ],
      correctAnswer: 'c'
    }
  ];

  return (
    <LessonLayout 
      title="   () -  35381"
      lessonId={lessonId}
      nextLessonPath={nextLessonPath}
    >
      {/* Learning Section */}
      <section id="learn" aria-labelledby="learn-heading" className="mb-12">
        <h2 id="learn-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
          :    
        </h2>
        <div className="space-y-4 text-gray-700 leading-relaxed">
          <p>     ,    .         .</p>
          <p>      <FormulaBox inline>y = ax^2 + bx + c</FormulaBox>,  <FormulaBox inline>a \\neq 0</FormulaBox>.</p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">1.     </h3>
          <p>    <FormulaBox inline>{"x_k = -\\frac{b}{2a}"}</FormulaBox>.     -<FormulaBox inline>y_k</FormulaBox>  ,   <FormulaBox inline>x_k</FormulaBox> .</p>
          <p>         ,  <FormulaBox inline>x = x_k</FormulaBox>.</p>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 my-6">
            <div className="p-4 border rounded-md bg-gray-50">
              <p className="text-center font-medium mb-2"> "" (<FormulaBox inline>{"a > 0"}</FormulaBox>) -  </p>
              <div className="bg-white p-4 rounded border text-center">
                <p className="text-sm text-gray-600">   </p>
                <p className="text-sm">   </p>
              </div>
            </div>            <div className="p-4 border rounded-md bg-gray-50">
              <p className="text-center font-medium mb-2"> "" (<FormulaBox inline>{"a < 0"}</FormulaBox>) -  </p>
              <div className="bg-white p-4 rounded border text-center">
                <p className="text-sm text-gray-600">   </p>
                <p className="text-sm">   </p>
              </div>
            </div>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">2.    </h3>
          <ul className="list-disc pr-5 space-y-2">
            <li>
              <strong>   Y:</strong>   <FormulaBox inline>x = 0</FormulaBox>. 
               <FormulaBox inline>x = 0</FormulaBox>   <FormulaBox inline>y = c</FormulaBox>. 
                 <FormulaBox inline>(0,c)</FormulaBox>.
            </li>
            <li>
              <strong>   X:</strong>   <FormulaBox inline>y = 0</FormulaBox>. 
               <FormulaBox inline>y = 0</FormulaBox>     <FormulaBox inline>ax^2 + bx + c = 0</FormulaBox>.
                ( )    <FormulaBox inline>\\Delta = b^2 - 4ac</FormulaBox>:
              <ul className="list-[circle] pr-5 space-y-1 mt-2">
                <li> <FormulaBox inline>{"\\Delta > 0"}</FormulaBox>:       X.</li>
                <li> <FormulaBox inline>\\Delta = 0</FormulaBox>:      X (    X).</li>
                <li> <FormulaBox inline>{"\\Delta < 0"}</FormulaBox>:      X ( ""      X).</li>
              </ul>
            </li>
          </ul>

          <h3 className="text-xl font-semibold mt-8 mb-2 text-gray-800">3.     </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 my-6">
            <div className="p-4 border rounded-md bg-blue-50">
              <p className="text-center font-medium mb-2"><FormulaBox inline>{"\\Delta > 0"}</FormulaBox></p>
              <p className="text-sm text-center">  -      X</p>
            </div>
            <div className="p-4 border rounded-md bg-yellow-50">
              <p className="text-center font-medium mb-2"><FormulaBox inline>\\Delta = 0</FormulaBox></p>
              <p className="text-sm text-center">  -    X</p>
            </div>
            <div className="p-4 border rounded-md bg-red-50">
              <p className="text-center font-medium mb-2"><FormulaBox inline>{"\\Delta < 0"}</FormulaBox></p>
              <p className="text-sm text-center">  -      X</p>
            </div>
          </div>

          <h3 className="text-xl font-semibold mt-8 mb-2 text-gray-800"> </h3>
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-6">
            <p className="font-medium"><strong>:</strong>    <FormulaBox inline>y = -x^2 + 4x - 3</FormulaBox></p>
            <p><strong>:</strong></p>
            
            <p><strong>.  :</strong> <FormulaBox inline>a = -1, b = 4, c = -3</FormulaBox></p>
            
            <p><strong>.  :</strong></p>
            <p> X  : <FormulaBox inline>{"x_k = -\\frac{b}{2a} = -\\frac{4}{2(-1)} = 2"}</FormulaBox></p>
            <p> Y  : <FormulaBox inline>y_k = -(2)^2 + 4(2) - 3 = -4 + 8 - 3 = 1</FormulaBox></p>
            <p>: <FormulaBox inline>(2,1)</FormulaBox></p>
            
            <p><strong>.  :</strong></p>
            <p>  Y:  <FormulaBox inline>x = 0</FormulaBox>  <FormulaBox inline>y = -3</FormulaBox>  : <FormulaBox inline>(0,-3)</FormulaBox></p>
            <p>  X:  <FormulaBox inline>-x^2 + 4x - 3 = 0</FormulaBox></p>
            <p>: <FormulaBox inline>{"\\Delta = 16 - 12 = 4 > 0"}</FormulaBox>    </p>
            
            <p><strong>.  :</strong>  -<FormulaBox inline>{"a = -1 < 0"}</FormulaBox>,  ""    .</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800"> </h3>
          <ul className="list-disc pr-6 space-y-1">
            <li>    a, b, c</li>
            <li>: a     (/)</li>
            <li> a    "" </li>
            <li>     </li>
            <li>     </li>
          </ul>
        </div>
      </section>

      {/* Practice Section */}
      <section id="practice" aria-labelledby="practice-heading" className="mb-12">
        <h2 id="practice-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        <div className="space-y-6">
          {exercises.map((exercise) => (
            <Exercise key={exercise.id} {...exercise} />
          ))}
        </div>
      </section>

      {/* Quiz Section */}
      <section id="quiz" aria-labelledby="quiz-heading" className="mb-12">
        <h2 id="quiz-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
             
        </h2>
        <Quiz questions={quizQuestions} lessonId={lessonId} />
      </section>
    </LessonLayout>
  );
};

export default FunctionsParabolaInvestigationLesson;
