import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const AnalyticGeometryCircleLesson = () => {

  const breadcrumbItems = [
    { label: ' ', href: '/' },
    { label: ' 35382', href: '/lessons' },
    { label: '', href: '#' }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: (        <>
               <FormulaBox inline>(2, 3)</FormulaBox>  5?
        </>
      ),
      options: [
        { value: 'a', label: '(x-2) + (y-3) = 25' },
        { value: 'b', label: '(x+2) + (y+3) = 25' },
        { value: 'c', label: '(x-2) + (y-3) = 5' },
        { value: 'd', label: 'x + y = 25' }
      ],
      correctAnswer: 'a'
    },
    {
      id: 'q2',
      question: (        <>
              <FormulaBox inline>(x+1)^2 + (y-4)^2 = 9</FormulaBox>?
        </>
      ),
      options: [
        { value: 'a', label: '(1, -4)' },
        { value: 'b', label: '(-1, 4)' },
        { value: 'c', label: '(1, 4)' },
        { value: 'd', label: '(-1, -4)' }
      ],
      correctAnswer: 'b'
    }
  ];

  const exercises = [
    {
      id: 'ex1',
      question: (        <>
                <FormulaBox inline>(3, -2)</FormulaBox>  4.
        </>
      ),
      correctAnswer: '(x-3)+(y+2)=16',
      placeholder: '  ',
      solution: (
        <div className="space-y-3">
          <p><strong>:</strong></p>          <p>  : <FormulaBox inline>(x-a)^2 + (y-b)^2 = R^2</FormulaBox></p>
          <p> <FormulaBox inline>(a,b)</FormulaBox>   -<FormulaBox inline>R</FormulaBox>  .</p>
          <p>: <FormulaBox inline>a = 3, b = -2, R = 4</FormulaBox></p>
          <p> :</p>
          <FormulaBox>(x-3)^2 + (y-(-2))^2 = 4^2</FormulaBox>
          <FormulaBox>(x-3)^2 + (y+2)^2 = 16</FormulaBox>
        </div>
      )
    },
    {
      id: 'ex2',
      question: (        <>
                <FormulaBox inline>x^2 + y^2 - 6x + 4y - 12 = 0</FormulaBox>.
        </>
      ),
      correctAnswer: '(3,-2),5',
      placeholder: '   (: (x,y),r)',
      solution: (
        <div className="space-y-3">
          <p><strong>:</strong></p>
          <p>    x -y:</p>
          <FormulaBox>x^2 - 6x + y^2 + 4y = 12</FormulaBox>
          <p>   x: <FormulaBox inline>x^2 - 6x = (x-3)^2 - 9</FormulaBox></p>
          <p>   y: <FormulaBox inline>y^2 + 4y = (y+2)^2 - 4</FormulaBox></p>
          <p>:</p>
          <FormulaBox>(x-3)^2 - 9 + (y+2)^2 - 4 = 12</FormulaBox>
          <FormulaBox>(x-3)^2 + (y+2)^2 = 12 + 9 + 4 = 25</FormulaBox>
          <p>: <FormulaBox inline>(3, -2)</FormulaBox></p>
          <p>: <FormulaBox inline>R = \sqrt{25} = 5</FormulaBox></p>
        </div>
      )
    }
  ];
  return (
    <LessonLayout 
      lessonId="analytic-geometry-circle"
      title=""
      nextLessonUrl="/lessons/problems-work-rate"
      menuUrl="/lessons"
    >
      {/* Learn Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
          :    
        </h2>
        
        <div className="space-y-4 text-gray-700 leading-relaxed">
          <p>
                      . 
                   .
          </p>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">
            1.   -  
          </h3>
            <p>
                <FormulaBox inline>(a, b)</FormulaBox>  <FormulaBox inline>R</FormulaBox> :
          </p>
          <FormulaBox>(x-a)^2 + (y-b)^2 = R^2</FormulaBox>
          
          <p>
                    :    {' '}
            <FormulaBox inline>(x,y)</FormulaBox>     <FormulaBox inline>(a,b)</FormulaBox> {' '}
            <FormulaBox inline>R</FormulaBox>.
          </p>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">
            2.   -  
          </h3>
            <p>
                 :
          </p>
          <FormulaBox>x^2 + y^2 + Dx + Ey + F = 0</FormulaBox>
          
          <p>
                  ,    .
          </p>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">
            3.  
          </h3>
          
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-6">
            <p className="font-medium">
              <strong>:</strong>       (2, -3)  5.
            </p>
            
            <p><strong>:</strong></p>            <p>1.   :  <FormulaBox inline>(a, b) = (2, -3)</FormulaBox>,  <FormulaBox inline>R = 5</FormulaBox>.</p>
            <p>2.    <FormulaBox inline>R=5</FormulaBox>.</p>
            <p>3.       <FormulaBox inline>(x-a)^2 + (y-b)^2 = R^2</FormulaBox>:</p>
            <FormulaBox>(x-2)^2 + (y-(-3))^2 = 5^2</FormulaBox>
            <FormulaBox>(x-2)^2 + (y+3)^2 = 25</FormulaBox>
            <p><strong>:</strong>    <FormulaBox inline>(x-2)^2 + (y+3)^2 = 25</FormulaBox>.</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800">
            4.  
          </h3>
          
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-6">            <p className="font-medium">
              <strong>:</strong>      <FormulaBox inline>x^2 + y^2 - 4x + 6y - 3 = 0</FormulaBox>.
            </p>
            
            <p><strong>:</strong></p>
            <p>1.   :</p>
            <FormulaBox>x^2 - 4x + y^2 + 6y = 3</FormulaBox>
            <p>2.    x:</p>
            <FormulaBox>x^2 - 4x = (x-2)^2 - 4</FormulaBox>
            <p>3.    y:</p>
            <FormulaBox>y^2 + 6y = (y+3)^2 - 9</FormulaBox>
            <p>4.  :</p>
            <FormulaBox>(x-2)^2 - 4 + (y+3)^2 - 9 = 3</FormulaBox>
            <FormulaBox>(x-2)^2 + (y+3)^2 = 3 + 4 + 9 = 16</FormulaBox>
            <p>5.    :</p>
            <ul className="list-disc pr-4">
              <li>  <FormulaBox inline>(2, -3)</FormulaBox>.</li>
              <li>  <FormulaBox inline>R = \sqrt{16} = 4</FormulaBox>.</li>
            </ul>
          </div>
        </div>
      </section>

      {/* Practice Section */}
      <section className="mb-12">
        <h2 className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        <div className="space-y-6">
          {exercises.map((exercise) => (
            <Exercise key={exercise.id} {...exercise} />
          ))}
        </div>
      </section>

      {/* Quiz Section */}
      <Quiz questions={quizQuestions} />
    </LessonLayout>
  );
};

export default AnalyticGeometryCircleLesson;
