import React from 'react';
import LessonLayout from '../components/LessonLayout';
import FormulaBox from '../components/FormulaBox';
import HtmlMathBox from '../components/HtmlMathBox';
import Exercise from '../components/Exercise';
import Quiz from '../components/Quiz';

const ProblemsBuySellLesson = () => {

  const lessonId = 'problems_buy_sell_he';
  const nextLessonPath = '/calculus-optimization';

  const exercises = [
    {
      id: 'ex1',
      question: '   -100      20%.     ?',
      correctAnswer: '120',
      placeholder: '  -',
      solution: (
        <div>
          <p><strong>:</strong></p>          <p> : 100 </p>
          <p>: 20% -100 = <FormulaBox inline>0.2 \times 100 = 20</FormulaBox> </p>
          <p>  =   +  = <FormulaBox inline>100 + 20 = 120</FormulaBox> </p>
        </div>
      )
    },
    {
      id: 'ex2',
      question: '   -90   10%   .      ?',
      correctAnswer: '100',
      placeholder: '  -',
      solution: (
        <div>
          <p><strong>:</strong></p>
          <p>    -x </p>
          <p>  10%   -90%  </p>
          <p>  = <FormulaBox inline>0.9x = 90</FormulaBox></p>
          <p>: <FormulaBox inline>x = \frac{90}{0.9} = 100</FormulaBox> </p>
        </div>
      )
    }
  ];

  const quizQuestions = [
    {
      id: 'q1',
      question: '   -80      25%.   ?',
      options: [
        { value: 'a', label: '100 ' },
        { value: 'b', label: '105 ' },
        { value: 'c', label: '120 ' },
        { value: 'd', label: '125 ' }
      ],
      correctAnswer: 'a'
    },
    {
      id: 'q2',
      question: '  -75     25%,    ?',
      options: [
        { value: 'a', label: '90 ' },
        { value: 'b', label: '95 ' },
        { value: 'c', label: '100 ' },
        { value: 'd', label: '110 ' }
      ],
      correctAnswer: 'c'
    }
  ];

  return (
    <LessonLayout 
      title="  "
      lessonId={lessonId}
      nextLessonPath={nextLessonPath}
    >
      {/* Learning Section */}
      <section id="learn" aria-labelledby="learn-heading" className="mb-12">
        <h2 id="learn-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
          :    
        </h2>
        <div className="space-y-4 text-gray-700 leading-relaxed">
          <p>         .          .</p>
          
          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800"> </h3>
          <ul className="list-disc pr-6 space-y-2">
            <li><strong>  ():</strong>       </li>
            <li><strong> :</strong>      </li>
            <li><strong>:</strong>   {'>'}  </li>
            <li><strong>:</strong>   {'<'}  </li>
          </ul>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800"> </h3>          <div className="space-y-3">
            <p><strong>/ :</strong></p>            <p className="text-center my-4">
              <span className="text-lg font-semibold">  = </span>              <HtmlMathBox inline>
                {"\\frac{\\text{}}{\\text{ }} \\times 100\\%"}
              </HtmlMathBox>
            </p>
            <p className="text-center my-4">
              <span className="text-lg font-semibold">  = </span>              <HtmlMathBox inline>
                {"\\frac{\\text{}}{\\text{ }} \\times 100\\%"}
              </HtmlMathBox>
            </p>
              <p><strong>   :</strong></p>            <p className="text-center my-4">
              <span className="text-lg font-semibold">  = </span>              <HtmlMathBox inline>
                {"\\text{ } \\times \\left(1 + \\frac{\\text{ }}{100}\\right)"}
              </HtmlMathBox>
            </p>
            
            <p><strong>   :</strong></p>
            <p className="text-center my-4">
              <span className="text-lg font-semibold">  = </span>              <HtmlMathBox inline>
                {"\\text{ } \\times \\left(1 - \\frac{\\text{ }}{100}\\right)"}
              </HtmlMathBox>
            </p>
          </div>

          <h3 className="text-xl font-semibold mt-8 mb-2 text-gray-800"> </h3>
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50 space-y-3 my-6">
            <p className="font-medium"><strong>:</strong>    -50 .       30%.    ?</p>
            <p><strong>:</strong></p>
            <p>1.  : 50 </p>
            <p>2.  : 30%</p>
            <p>3.  : <FormulaBox inline>50 \times 0.3 = 15</FormulaBox> </p>
            <p>4.  : <FormulaBox inline>50 + 15 = 65</FormulaBox> </p>
            <p> : <FormulaBox inline>50 \times 1.3 = 65</FormulaBox> </p>
            <p><strong>:</strong>    65 .</p>
          </div>

          <h3 className="text-xl font-semibold mt-6 mb-2 text-gray-800"> </h3>
          <ol className="list-decimal pr-6 space-y-2">
            <li>     </li>
            <li>     </li>
            <li>  </li>
            <li>  </li>
          </ol>
        </div>
      </section>

      {/* Practice Section */}
      <section id="practice" aria-labelledby="practice-heading" className="mb-12">
        <h2 id="practice-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
           
        </h2>
        <div className="space-y-6">
          {exercises.map((exercise) => (
            <Exercise key={exercise.id} {...exercise} />
          ))}
        </div>
      </section>

      {/* Quiz Section */}
      <section id="quiz" aria-labelledby="quiz-heading" className="mb-12">
        <h2 id="quiz-heading" className="text-2xl font-semibold text-purple-600 mb-4 border-b-2 border-purple-200 pb-2">
                     </h2>
        <Quiz questions={quizQuestions} lessonId={lessonId} />
      </section>
    </LessonLayout>
  );
};

export default ProblemsBuySellLesson;
